import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Salle from '../views/Salle.vue'
import Order from '../views/Order.vue'
import Management from '../views/Management.vue'
import Delivery from '../views/Delivery.vue'
import DeliveryOrder from '../views/DeliveryOrder.vue'
import Arrangement from '../components/Arrangement'
// import Test from '../components/WechatList.vue'
import Test from '../views/Test.vue'
import PrintListen from '../views/PrintListen'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/salle',
    component: Salle
  },
  {
    path: '/order/:table_num',
    component: Order
  },
  {
    path: '/Management',
    component: Management
  },
  {
    path: '/delivery',
    component: Delivery
  },
  {
    path: '/arrangement',
    component: Arrangement
  },
  {
    path: '/delivery/:ticket_id',
    component: DeliveryOrder
  },
  {
    path: '/sakinda',
    component: Test
  },
  {
    path: '/printlisten',
    component: PrintListen
  },
  {
    path: '*',
    component: Home
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
