<template>
  <div class="outer2">
    <div class="header">
      <i class="iconfont icon-zuojiantou"
      style="text-align: right; margin-left: 0px; border: 2px solid white; border-radius: 50%; font-size:2em; color: black; font-weight: bolder"
      @click="handleShow">
      </i>
      <p>桌号：{{ ticketDetail.table_num }}</p>
      <div @click="payingDishShow = !payingDishShow" class="payingAmount">辅助：{{ payingAmount }}</div>
    </div>

    <transition name='paying'>
        <div class="payingDish" v-show="payingDishShow">
          <div @click="payingDishShow = !payingDishShow" style="text-align: right; margin-right:10px; font-size: 30px">x</div>
          <div style="display: grid; grid-template-columns: 1fr 2fr; align-items: center; border-bottom: 1px solid rgba(255, 255, 255, 0.5)">
            <p @click="payingList=[]">重置</p>
            <h3 class='payingTotal'>总：{{ payingAmount }}</h3>
          </div>

          <ul style="margin-top: 5px">
            <li @click="handlePayingDel(data)" class="payingDetail" v-for="data in payingList" :key="data.id">
              <p>{{ data.name }}</p>
              <p>{{ data.price }}</p>
            </li>
          </ul>
        </div>
      </transition>

    <div class="stickyArea">
      <div id='total'>
        <div id="tax10" class="tax">
          <p class="taxTitle">10%</p>
          <p>{{ tatalTax10 }}</p>
        </div>
        <div id="tax20" class="tax">
          <p class="taxTitle">20%</p>
          <p>{{ tatalTax20 }}</p>
        </div>
        <div id="totalTax" class="tax">
          <p class="taxTitle">总TVA</p>
          <p>{{ tatalTax }}</p>
        </div>
        <div id="originalPrice" class="tax">
          <p class="taxTitle">原价</p>
          <p>{{ originalPrice }}</p>
        </div>
        <div id="discount" class="tax">
          <p class="taxTitle">折扣</p>
          <input type="number" v-model="ticketDetail.ticket_discount"
          @focus="focus($event)"
          style="width:35px;height:20px; border: 1px solid rgba(7, 17, 27, 0.1);text-align:center"
          @change="postDiscount">
          <!-- <p>{{ ticketDetail.ticket_discount }}</p> -->
        </div>
        <div id="reduction" class="tax">
          <p class="taxTitle">减免</p>
          <input type="number" v-model="ticketDetail.ticket_reduction"
          @focus="focus($event)"
          style="width:35px;height:20px; border: 1px solid rgba(7, 17, 27, 0.1);text-align:center"
          @change="postReduction">
        </div>
        <h2 id='totalPrice'>总：{{ this.totalPrice }}</h2>
      </div>

      <div class="detailArea">
          <div class="detail" style="color: #2CA4B0">
            <p class="title">转账</p>
            <p class="amount">{{ ticketDetail.pay_by_transfer }}</p>
          </div>
          <div class="detail" style="color: #ff4a4a">
            <p class="title">现金</p>
            <p class="amount">{{ ticketDetail.pay_by_cash }}</p>
          </div>
          <div class="detail" style="color: #217969">
            <p class="title">饭票</p>
            <p class="amount">
              {{ ticketDetail.pay_by_ticket }}
            </p>
            <p style="font-size:10px; margin-top:-5px">({{ ticketDetail.ticket_restaurant_count }}张)</p>
          </div>
          <div class="detail" style="color: #b48608">
            <p class="title">刷卡</p>
            <p class="amount">{{ ticketDetail.pay_by_card }}</p>
          </div>
          <div class="detail">
            <p class="title">支票</p>
            <p class="amount">{{ ticketDetail.pay_by_check }}</p>
          </div>
          <div class="detail">
            <p class="title">微信</p>
            <p class="amount">{{ ticketDetail.pay_by_wechat }}</p>
          </div>
          <div class="detail">
            <p class="title">阿里</p>
            <p class="amount">{{ ticketDetail.pay_by_alipay }}</p>
          </div>
      </div>
      <!-- <div class="pay">
        <div class="totalPay">已付：</div>
        <div class="restPay">剩余：</div>
      </div> -->
    </div>

    <div class="handleArea">
      <div class="handlePay">
        <div class="payAmount">
          <h5>金额：</h5>
          <input ref="inputArea" type="number" @focus="focus($event)" style="width: 60px; height: 40px; text-align: left; border: 1px solid rgba(7, 17, 27, 0.1)" v-model="inputAmount">
          <div class="totalPay" style="font-size:14px">已付：{{ alreadyPay }}</div>
          <div class="restPay" style="font-size:14px">剩余：{{ restPay }}</div>
          <div class="returnPay" style="font-size:14px">找零：{{ returnPay }}</div>
        </div>
        <div class="payButton">
          <v-btn @click="handlePayByVirement">转账</v-btn>
          <v-btn @click="handlePayByCash">现金</v-btn>
          <div class="history" style='grid-row:1/6; grid-column:3/4; background-color: #eee; border-radius:10px'>
            <p>付款记录</p>
            <!-- {{ ticketDetail.payment_history.split(',') }} -->
            <ul>
              <li v-for="item in paymentHistory" :key="item" style="display: flex; justify-content:space-between">
                <p>{{ historyMethod(item) }}</p>
                <p>{{ historyAmount(item) }}</p>
              </li>
            </ul>
          </div>
          <v-btn @click="handlePayByTicket">饭票</v-btn>
          <v-btn @click="handlePayByCard">刷卡</v-btn>
          <v-btn @click="handlePayByCheck">支票</v-btn>
          <v-btn @click="handlePayByWechat">微信</v-btn>
          <v-btn @click="handlePayByAlipay">阿里</v-btn>
          <v-btn @click="changeTicketRestoCount">饭票张数</v-btn>
        </div>
      </div>
    </div>

    <div class='ordered_list' ref="orderWrapper">
      <ul>
        <li @click="handlePayingAdd(data)" class="order_container" v-for="data in selectedDish" :key="data.order_id">
          <h4 class="dish_name">{{ Object.keys(data)[0] }}</h4>
          <p class="price">{{ Object.values(data)[0].price }}</p>
          <p class="quantity">{{ Object.values(data)[0].quantity }}份</p>
          <p class="subTotal">{{ (Object.values(data)[0].price * Object.values(data)[0].quantity).toFixed(2) }}</p>
        </li>
      </ul>
    </div>

    <div class="buttonArea">
        <v-btn dark rounded color="#217969" width="50%" @click="handleSubmit">
          <!-- <v-icon>mdi-pencil</v-icon> -->
          <span>提交</span>
        </v-btn>
        <v-btn dark rounded color="#ff4a4a" @click="showPopup" width="50%">删除</v-btn>
    </div>

    <!-- <van-cell is-link @click="showPopup">展示弹出层</van-cell> -->
    <van-popup class="confirmArea" v-model="show" id='delete_submit' round position="bottom" :style="{ height: '30%', width: '100%'}">
      <v-btn dark rounded color="#ff4a4a" width="50%" @click="handleDelete">删除</v-btn>
      <v-btn rounded width="50%" @click="show = false">取消</v-btn>
    </van-popup>
  </div>
</template>

<script>
import axios from 'axios'
import BScroll from 'better-scroll'
import { VBtn } from 'vuetify/lib/components'
import 'vuetify/dist/vuetify.min.css'
// import 'material-design-icons-iconfont/dist/material-design-icons.css'
// import 'material'
import Vue from 'vue'
import { Popup } from 'vant'

Vue.use(Popup)
// Vue.use(VIcon, {
//   icons: {
//     iconfont: 'md'
//   }
// })

export default {
  props: ['payShow', 'ticketId'],
  components: {
    // BScroll,
    VBtn
    // VIcon
  },
  data () {
    return {
      inputAmount: '',
      reduction: 0,
      ticketDetail: {},
      selectedDish: [],
      payCondition: {},
      payingList: [],
      payingDishShow: false,
      show: false
    }
  },
  mounted () {
    // this.$nextTick(() => {
    //   console.log('Mounted中绑定的')
    //   this._initScroll()
    // })
  },
  computed: {
    // 付款记录中的付款方式
    historyMethod () {
      return function (item) {
        if (this.ticketDetail.payment_history) {
          console.log(item)
          let res = item.split(':')
          res = res[0].split('_')
          return res[2]
        } else {
          return ''
        }
      }
    },

    // 付款记录中的付款金额
    historyAmount () {
      return function (item) {
        if (this.ticketDetail.payment_history) {
          let res = item.split(':')
          res = res[1].split('}')
          return res[0]
        } else {
          return ''
        }
      }
    },

    paymentHistory () {
      if (this.ticketDetail.payment_history) {
        return this.ticketDetail.payment_history.split(',')
      } else {
        return ''
      }
    },
    payingAmount () {
      let total = 0
      this.payingList.forEach((item) => {
        total += parseFloat(item.price)
      })
      return total.toFixed(2)
    },
    totalPrice () {
      // console.log('test:::::::::', this.$store.state.selectedDish)
      if (this.selectedDish.length !== undefined) {
        return (this.originalPrice * this.ticketDetail.ticket_discount - this.ticketDetail.ticket_reduction).toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax10 () {
      // console.log('测试：：：：：：：：：：：', this.$store.state.selectedDish.length)
      let total = 0
      if (this.selectedDish.length !== undefined) {
        for (const data of this.selectedDish) {
          if (Object.values(data)[0].tax === '0.10') {
            total += (Object.values(data)[0].price - (Object.values(data)[0].price / (1 + parseFloat(Object.values(data)[0].tax)))) * Object.values(data)[0].quantity
          }
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax20 () {
      let total = 0
      if (this.selectedDish.length !== undefined) {
        for (const data of this.selectedDish) {
          if (Object.values(data)[0].tax === '0.20') {
            total += Object.values(data)[0].tax * Object.values(data)[0].quantity
          }
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax () {
      return (parseFloat(this.tatalTax10) + parseFloat(this.tatalTax20)).toFixed(2)
    },
    originalPrice () {
      let total = 0
      if (this.selectedDish.length !== undefined) {
        for (const data of this.selectedDish) {
          // console.log(data)
          total += Object.values(data)[0].price * Object.values(data)[0].quantity
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    alreadyPay () {
      let total = 0
      Object.keys(this.ticketDetail).forEach(item => {
        if (item.includes('pay_by')) {
          total += parseFloat(this.ticketDetail[item])
          // console.log(total)
        }
      })
      return total
    },
    restPay () {
      const total = this.totalPrice - this.alreadyPay
      return total.toFixed(2)
    },
    returnPay () {
      if (this.restPay < 0) {
        if (this.ticketDetail.pay_by_cash >= (-this.restPay)) {
          return -this.restPay
        } else if (this.ticketDetail.pay_by_cash > 0 && this.ticketDetail.pay_by_cash < (-this.restPay)) {
          return this.ticketDetail.pay_by_cash
        } else {
          return 0
        }
      } else {
        return 0
      }
    }
  },
  methods: {
    handleShow () {
      this.orderScroll = null
      // console.log(this.orderScroll)
      this.$emit('hidedetail')
    },
    focus (event) {
      event.currentTarget.select()
    },
    handlePayClick () {
      this.payingList = []
      this.$store.commit('changePayShow')
    },
    _initScroll () {
      this.orderScroll = new BScroll('.ordered_list', {
        scrollY: true,
        click: true
      })

      // 获取点击的dom元素
      // const dish = this.$refs.orderWrapper
      // dish.addEventListener('click', (evt) => {
      //   console.log(evt.target.parentNode)
      // })
    },
    // 转账支付
    handlePayByVirement () {
      if (this.inputAmount !== '') {
        // this.ticketDetail.pay_by_transfer = ((Number(this.ticketDetail.pay_by_transfer)) + Number(this.inputAmount)).toFixed(2)
        this.pay('pay_by_transfer', this.ticketDetail.pay_by_transfer)
      }
    },
    // 现金支付
    handlePayByCash () {
      if (this.inputAmount !== '') {
        // this.ticketDetail.pay_by_cash = ((Number(this.ticketDetail.pay_by_cash)) + Number(this.inputAmount)).toFixed(2)
        this.pay('pay_by_cash', this.ticketDetail.pay_by_cash)
      }
    },
    handlePayByTicket () {
      if (this.inputAmount !== '') {
        // this.ticketDetail.pay_by_ticket = ((Number(this.ticketDetail.pay_by_ticket)) + Number(this.inputAmount)).toFixed(2)
        this.pay('pay_by_ticket', this.ticketDetail.pay_by_ticket)
      }
    },
    handlePayByCard () {
      if (this.inputAmount !== '') {
        // this.ticketDetail.pay_by_card = ((Number(this.ticketDetail.pay_by_card)) + Number(this.inputAmount)).toFixed(2)
        this.pay('pay_by_card', this.ticketDetail.pay_by_card)
      }
    },
    handlePayByCheck () {
      if (this.inputAmount !== '') {
        // this.ticketDetail.pay_by_check = ((Number(this.ticketDetail.pay_by_check)) + Number(this.inputAmount)).toFixed(2)
        this.pay('pay_by_check', this.ticketDetail.pay_by_check)
      }
    },
    handlePayByWechat () {
      if (this.inputAmount !== '') {
        // this.ticketDetail.pay_by_wechat = ((Number(this.ticketDetail.pay_by_wechat)) + Number(this.inputAmount)).toFixed(2)
        this.pay('pay_by_wechat', this.ticketDetail.pay_by_wechat)
      }
    },
    handlePayByAlipay () {
      if (this.inputAmount !== '') {
        // this.ticketDetail.pay_by_alipay = ((Number(this.ticketDetail.pay_by_alipay)) + Number(this.inputAmount)).toFixed(2)
        this.pay('pay_by_alipay', this.ticketDetail.pay_by_alipay)
      }
    },
    handleSubmit () {
      if (this.restPay === '0.00') {
        this.ticketDetail.payment_status = 2
        axios({
          url: `/testticketdetailpaied/${this.ticketId}/`,
          method: 'PATCH',
          data: this.ticketDetail
        }).then((res) => {
          // alert('提交成功')
          // this.$router.push('/delivery')
          this.$router.go(0)
        }).catch((err) => console.log(err))
      } else {
        alert(`剩余${this.restPay}未结清`)
      }
    },
    showPopup () {
      this.show = true
    },
    handleDelete () {
      axios({
        url: `/testticketdetailpaied/${this.ticketId}/`,
        method: 'DELETE'
      }).then((res) => {
        alert('删除成功')
        this.$router.push('/salle')
      }).catch((err) => console.log(err))
    },
    handlePayingAdd (data) {
      // console.log(Object.keys(data)[0])
      // console.log(Object.values(data)[0].price)
      const date = new Date()
      const dish = {}
      Vue.set(dish, 'id', date.getTime())
      Vue.set(dish, 'name', Object.keys(data)[0])
      Vue.set(dish, 'price', Object.values(data)[0].price)
      this.payingList.push(dish)
      console.log(this.payingList)
    },
    handlePayingDel (data) {
      console.log(this.payingList.indexOf(data))
      const index = this.payingList.indexOf(data)
      this.payingList.splice(index, 1)
    },

    pay (method, amount) {
      if (this.inputAmount !== '') {
        const num = (Number(amount) + Number(this.inputAmount)).toFixed(2)
        const content = '{' + '"' + method + '"' + ': ' + num + '}'
        console.log(content)
        console.log(JSON.parse(content))
        const contentObj = JSON.parse(content)
        // contentObj.payment_history = []
        // this.ticketDetail.payment_history.split(',').forEach(item => { contentObj.payment_history.push(item) })
        // const content2 = '{' + '"' + method + '"' + ': ' + Number(this.inputAmount).toFixed(2) + '}'
        // contentObj.payment_history.push(content2)
        // contentObj.payment_history = JSON.stringify(contentObj.payment_history)
        contentObj.payment_history = this.ticketDetail.payment_history
        // console.log(typeof (method))
        if (contentObj.payment_history === '') {
          contentObj.payment_history = ('{' + method + ':' + this.inputAmount + '}')
        } else {
          contentObj.payment_history += (',{' + method + ':' + this.inputAmount + '}')
        }
        console.log(contentObj)

        axios({
          url: `/testpatchticket/${this.ticketId}/`,
          method: 'PATCH',
          data: contentObj
        }).then(() => {
          // console.log('TEST::::::::::')
          this.refreshPay(this)
        })
          .catch(err => console.log(err))
        // that.inputAmount = ''
        this.$refs.inputArea.select()
      }
    },

    changeTicketRestoCount () {
      console.log(typeof (this.inputAmount) === 'number')
      console.log(this.inputAmount % 1 === 0)
      if (this.inputAmount !== '' && typeof Number(this.inputAmount) === 'number' && this.inputAmount % 1 === 0) {
        console.log('判断为真')
        const num = Number(this.ticketDetail.ticket_restaurant_count) + Number(this.inputAmount)
        const content = '{' + '"ticket_restaurant_count"' + ': ' + num + '}'
        const contentObj = JSON.parse(content)
        axios({
          url: `/testpatchticket/${this.ticketId}/`,
          method: 'PATCH',
          data: contentObj
        }).then(() => {
          this.refreshPay(this)
        })
          .catch(err => console.log(err))
        this.$refs.inputArea.select()
      }
    },

    refreshPay (that) {
      async function getPay () {
        try {
          const response = await axios({
            url: `/testticketdetailpaied/${that.ticketId}/`
          })
          console.log(response.data)
          that.ticketDetail = response.data
        } catch (err) { console.log(err) }
      }
      getPay()
    },

    postReduction () {
      // const content = '{' + '"' + 'ticket_reduction' + '"' + ': ' + this.ticketDetail.ticket_reduction + '}'
      // if (this.ticketDetail.ticket_reduction === '' || this.ticketDetail.ticket_reduction > this.originalPrice) {
      //   this.ticketDetail.ticket_reduction = 0
      // }
      const content = {
        ticket_reduction: this.ticketDetail.ticket_reduction,
        ticket_price: this.totalPrice
      }
      // console.log(JSON.parse(content))
      axios({
        url: `/testpatchticket/${this.ticketId}/`,
        method: 'PATCH',
        data: content
      }).then(() => {
        // console.log('TEST::::::::::')
        this.refreshPay(this)
      })
        .catch(err => console.log(err))
    },

    // POST 减免金额
    postDiscount () {
      // const content = '{' + '"' + 'ticket_reduction' + '"' + ': ' + this.ticketDetail.ticket_reduction + '}'
      // if (this.ticketDetail.ticket_discount === '') {
      //   this.ticketDetail.ticket_discount = 1
      // }
      const content = {
        ticket_discount: this.ticketDetail.ticket_discount,
        ticket_price: this.totalPrice
      }
      // console.log(JSON.parse(content))
      axios({
        url: `/testpatchticket/${this.ticketId}/`,
        method: 'PATCH',
        data: content
      }).then(() => {
        // console.log('TEST::::::::::')
        this.refreshPay(this)
      })
        .catch(err => console.log(err))
    },

    // 付款记录中的付款方式，已弃用，改用computed
    historyMethod2 (item) {
      if (this.ticketDetail.payment_history) {
        console.log(item)
        let res = item.split(':')
        res = res[0].split('_')
        return res[2]
      }
    },

    // 付款记录中的付款金额，已弃用，改用computed
    historyAmount2 (item) {
      console.log(item)
      if (this.ticketDetail.payment_history) {
        let res = item.split(':')
        res = res[1].split('}')
        return res[0]
      } else {
        return ''
      }
    }
  },

  watch: {
    'payShow' () {
      axios({
        url: `/testticketdetailpaied/${this.ticketId}/`
      }).then((res) => { this.ticketDetail = res.data }).catch((err) => console.log(err))

      axios({
        url: `/testticketordergroupby2/${this.ticketId}/`
      }).then(res => {
        console.log(res.data)
        this.selectedDish = res.data.map(function (item) {
          var Obj = {}
          // Obj[item.dname] = item.total_quantity[0]
          Obj[item.dname] = { quantity: item.total_quantity[0], price: item.dprice, tax: item.dtax }
          return Obj
        })
        this.$nextTick(() => {
          if (this.detailShow) {
            if (!this.orderScroll) {
              this._initScroll()
              // console.log('生成了新的Bs')
            } else {
              this.orderScroll.refresh()
              // console.log('刷新了Bs')
            }
            // console.log('detail为true')
          }
        })
      }).catch((err) => console.log(err))
    }
  }
}

</script>

<style lang="scss" scoped>
  .outer2 {
    position: fixed;
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    top: 0px;
    z-index: 10;
    padding: 0 0px;
    background-color: #fff;
    overflow-y: auto;
    .header {
      position: fixed;
      top: 0px;
      width: 100%;
      background-color: #fff;
      display: grid;
      grid-template-columns: 1fr 2fr 8fr;
      align-items: center;
      // justify-content: space-evenly;
      .icon-shangjiantou1 {
      transform: rotate(180deg);
      // flex: 0 0 60px;
      }
      .payingAmount {
        text-align: right;
        padding-right: 10px;
      }
    }

    .payingDish {
        position: fixed;
        top: 10px;
        right: 10px;
        width: 200px;
        height: 400px;
        color: white;
        background-color: rgba(7, 17, 27, 0.8);
        z-index: 11;
        border-radius: 10px;
        overflow: hidden;
        overflow-y: auto;
        .payingDetail {
          // width: 200px;
          display: grid;
          align-items: center;
          grid-template-columns: 5fr 2fr;
          grid-auto-rows: 1.5em;
          font-size: 18px;
          p {
            white-space:nowrap;
            text-overflow:ellipsis;
            overflow:hidden;
          }
        }
      }

      .paying-enter-active {
        animation: toggle 0.2s;
      }

      .paying-leave-active {
        animation: toggle 0.2s reverse;
      }

      @keyframes toggle {
        0% {
          width: 0px;
          height: 0px;
        }
        100% {
          width: 200px;
          height: 400px;
        }
      }

    .stickyArea {
      position: relative;
      top: 35px;
      height: 100px;
      background-color: #fff;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      width: 100%;
      box-sizing: border-box;
      // border: 1px solid black;
      margin: 0;
      #total {
        padding: 5px 5px;
        width: 100%;
        height: 42px;
        display: flex;
        align-items: center;
        // border-bottom: 1px solid #eee;
        // flex-direction: row-reverse;
        .taxTitle {
          font-size: 10px;
