<script>
import axios from 'axios'
import Detail from './Management/Detail'
import BScroll from 'better-scroll'
import TicketList from '../components/TicketList.vue'
import Summary from '../components/Summary'

async function getTicket (_this) {
  const res = await axios({
    url: '/testticketlistpaied/'
  })
  _this.ticketList = Object.values(res.data)
  console.log(_this.ticketList)
  _this.$nextTick(() => {
    if (!_this.orderScroll) {
      _this._initScroll()
      // console.log('创建了新的Bs')
    } else {
      _this.orderScroll.refresh()
      // console.log('刷新了Bs')
    }
    // console.log('Bs已生效')
  })
}

export default {
  components: {
    Detail,
    TicketList,
    Summary
  },
  data () {
    return {
      activeTab: 3,
      ticketList: [],
      ticketDetail: {},
      selectedDish2: [],
      detailShow: false,
      ticketId: ''
    }
  },
  mounted () {
    // const _this = this
    // console.log(this)
    getTicket(this)
  },
  computed: {
    // 计算订单类别
    type () {
      return function (data) {
        if (data.type === 0) {
          return '堂食'
        } else if (data.type === 1) {
          return '打包'
        } else {
          return '外送'
        }
      }
    },
    // 计算订单标题
    title () {
      return function (data) {
        if (data.type === 0) {
          return '桌号：' + data.table_num
        } else if (data.type === 1) {
          return '打包'
        } else {
          return '外送'
        }
      }
    },
    totalPrice () {
      // console.log('test:::::::::', this.$store.state.selectedDish)
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          // console.log(data)
          total += parseFloat(data.ticket_price)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax10 () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.ticket_tax10)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax20 () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.ticket_tax20)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax () {
      return (parseFloat(this.tatalTax10) + parseFloat(this.tatalTax20)).toFixed(2)
    },
    totalVirement () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_transfer)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalCash () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_cash)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalTicket () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_ticket)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalCard () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_card)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalCheck () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_check)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalWechat () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_wechat)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalAlipay () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_alipay)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    }
  },
  methods: {
    onHandleDetailShow (data) {
      this.ticketId = data.ticket_id
      // console.log(data)
      this.detailShow = !this.detailShow
      // axios({
      //   url: `/testticketdetailpaied/${data.ticket_id}/`
      // }).then((res) => { this.ticketDetail = res.data }).catch((err) => console.log(err))
      // axios({
      //   url: `/testticketordergroupby2/${data.ticket_id}/`
      // }).then(res => {
      //   console.log(res.data)
      //   this.selectedDish = res.data.map(function (item) {
      //     var Obj = {}
      //     // Obj[item.dname] = item.total_quantity[0]
      //     Obj[item.dname] = { quantity: item.total_quantity[0], price: item.dprice, tax: item.dtax }
      //     return Obj
      //   }).then((res) => {
      //     this.$nextTick(() => {
      //       if (this.detailShow) {
      //         if (!this.orderScroll) {
      //           this._initScroll()
      //           console.log('生成了新的Bs')
      //         } else {
      //           this.orderScroll.refresh()
      //           console.log('刷新了Bs')
      //         }
      //         console.log('detail为true')
      //       }
      //     })
      //   })
      // }).catch((err) => console.log(err))
    },
    onHideDetail (data) {
      getTicket(this)
      this.detailShow = false
    },
    _initScroll () {
      this.orderScroll = new BScroll('.ticketList', {
        scrollY: true,
        click: true
      })
    },
    async getTicket () {
      const res = await axios({
        url: '/testticketlistpaied/'
      })
      // console.log(res)
      if (this.activeTab === 3) {
        this.ticketList = Object.values(res.data)
      } else {
        this.ticketList = Object.values(res.data).filter(item => item.type === this.activeTab)
      }
      // console.log(this.ticketList)
      this.$nextTick(() => {
        if (!this.orderScroll) {
          this._initScroll()
          // console.log('创建了新的Bs')
        } else {
          this.orderScroll.refresh()
          // console.log('刷新了Bs')
        }
      })
    }
  },

  watch: {
    activeTab: function () {
      switch (this.activeTab) {
        case 0:
          console.log('tab===2')
          this.getTicket()
          break
        case 1:
          console.log('tab===1')
          this.getTicket()
          break
        case 2:
          console.log('tab===2')
          this.getTicket()
          // this.ticketList = this.ticketList.filter((item) => item.type === 2)
          break
        case 3:
          console.log('tab===3')
          this.getTicket()
          break
        default:
          this.ticketList = this.ticketList.filter((item) => item.type === 2)
          break
      }
    }
  }
}
</script>

