<template>
  <div class="outWrapper">

    <div class="categoryContainer" ref="categoryContainer">
      <ul>
        <!-- <li v-for="category in foodList" :key="category.subcategory_id"
        :class="[category.subcategory_id == categoryId ? 'categorySelect' : 'categoryNoselect']"
        @click="categoryId = category.subcategory_id" class="category-list"> -->
        <li v-for="(category, $index) in foodList" :key="category.subcategory_id"
        :class="[currentIndex3 === $index ? 'categorySelect' : 'categoryNoselect']"
        @click="selectFood($index)" class="category-list">
        <!-- scrollY = listHeight[$index] -->
          <span>{{ category.subcategory }}</span>
        </li>
      </ul>
    </div>

    <div class="dishContainer" ref="dishWrapper">
      <ul>
        <li v-for="category in foodList" :key="category.subcategory_id" class="categoryList category-list-hook">
          <h3 class="title">{{ category.subcategory }}</h3>
          <ul>
            <li v-for="food in category.foods" :key="food.did" class="foodList">
              <div class="firstline">
                <p style="font-weight: bold">{{ food.dname }}</p>
                <p v-show="food.selected">已选 {{ food.selected }}</p>
              </div>
              <div class="secondline">
                <p style="color: #ff4a4a">{{ food.dprice }}</p>
                <div>
                  <add-minus :food='food'></add-minus>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
import BScroll from 'better-scroll'
import AddMinus from '../../components/AddMinus'

async function axiosCreat (orderData, _this) {
  console.log(orderData)
  try {
    await axios({
      url: '/testcreate/',
      method: 'POST',
      data: orderData
    })
    console.log('创建成功', orderData.code)
  } catch (err) {
    console.log(err)
  }
  // getSubCategory(_this)
  // _this.$parent.getTicketOrder()
}

async function submitOrder (_this) {
  console.log(_this)
  await _this.foodList.forEach(category => {
    category.foods.forEach(food => {
      if (food.selecting) {
        var date = new Date()
        var orderTime = date.getFullYear().toString() +
                        (((date.getMonth() + 1).toString().length < 2) ? '0' + (date.getMonth() + 1).toString() : (date.getMonth() + 1).toString()) +
                        date.getDate().toString() +
                        (((date.getHours()).toString().length < 2) ? '0' + date.getHours().toString() : date.getHours().toString()) +
                        (((date.getMinutes()).toString().length < 2) ? '0' + date.getMinutes().toString() : date.getMinutes().toString()) +
                        (((date.getSeconds()).toString().length < 2) ? '0' + date.getSeconds().toString() : date.getSeconds().toString())
        const orderData = {}
        Vue.set(orderData, 'order_id', 'D' + date.getTime().toString() + Math.floor(Math.random() * (999 - 100 + 1) + 100).toString())
        Vue.set(orderData, 'order_time', orderTime)
        Vue.set(orderData, 'quantity', food.selecting)
        Vue.set(orderData, 'code', food.dcode)
        Vue.set(orderData, 'tid', _this.ticket_id)
        Vue.set(orderData, 'o_note', '无备注')
        console.log(JSON.stringify(orderData))
        console.log(orderData)
        try {
          axiosCreat(orderData, _this)
        } catch (err) {
          console.log(err)
        }
      }
    })
  })
  setTimeout(() => {
    getSubCategory(_this)
    _this.$parent.getTicketOrder()
    _this.$parent.getTicketBasicDetail()
  }, 300)
}

async function getSubCategory (_this) {
  // let foodList = []
  const subCList = await axios('/testsubcategory/')

  const DList = await axios('/testalldish/')
  // console.log(DList.data.length)

  if (_this.ticket_id) {
    try {
      const TDetail = await axios('/testticketordergroupby2/' + _this.ticket_id + '/')
      // console.log(TDetail.data)

      subCList.data.forEach((sub) => {
        sub.foods = []
        DList.data.forEach((dish) => {
          if (dish.dsubcategory === sub.subcategory_id) {
            sub.foods.push(dish)
            // console.log(sub.foods.indexOf(dish))
            // console.log(sub.foods)
            TDetail.data.forEach((selectedDish) => {
              // console.log(selectedDish.dname)
              // console.log(dish.dname)
              if (selectedDish.dname === dish.dname) {
                Vue.set(sub.foods[sub.foods.indexOf(dish)], 'selected', selectedDish.total_quantity[0])
              }
            })
            // const i = DList.data.findIndex(d => d.dname === dish.dname)
            // DList.data.splice(i, 1) //想用来去重，未成功，待研究
          }
        })
      })
      // console.log(subCList.data)
      _this.foodList = subCList.data // 获取到改装后的数组
      _this.ticketDetail = TDetail
    } catch (err) {
      console.log(err)
    }
  }

  // console.log('执行非method中的方法')
  // 为category和dish增加滚动效果
  _this.$nextTick(() => {
    if (!_this.categoryScroll) {
      _this._initScroll()
      // setTimeout(() => {
      //   _this._calculateHeight()
      // }, 4000)
      _this._calculateHeight()
      // console.log('创建了新的Bs')
      // alert('创建了新的Bs')
    } else {
      _this.categoryScroll.refresh()
      // console.log('刷新了Bs')
    }
  })
}

export default {
  props: ['ticket_id', 'wechatTicket'],
  components: {
    AddMinus
  },
  data () {
    return {
      categoryId: '101',
      foodList: [], // 包含菜品groupby信息
      ticketDetail: [],
      listHeight: [],
      scrollY: 0
    }
  },
  mounted () {
    // this.getSubCategory()
  },
  computed: {
    currentIndex3 () {
      // console.log(this.listHeight.length)
      for (let i = 0; i < this.listHeight.length; i++) {
        const height1 = this.listHeight[i]
        const height2 = this.listHeight[i + 1]
        // console.log(this.listHeight[i + 1])
        // console.log(height1)
        // console.log(height2)
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          // console.log('if判断为真，computed就失效')
          return i
        }
      }
      console.log('由于无height2，所以无法返回cunrrentIndex')
      return 0
    }
  },
  methods: {
    currentIndex2 () {
      console.log(this.listHeight.length)
      for (let i = 0; i < this.listHeight.length; i++) {
        const height1 = this.listHeight[i]
        const height2 = this.listHeight[i + 1]
        // console.log(this.listHeight[i + 1])
        // console.log(height1)
        // console.log(height2)
        if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
          // console.log('if判断为真')
          return i
        }
      }
      console.log('由于无height2，所以无法返回cunrrentIndex')
      return 0
    },
    async getSubCategory () {
      // let foodList = []
      const subCList = await axios('/testsubcategory/')

      const DList = await axios('/testalldish/')
      // console.log(DList.data.length)

      if (this.ticket_id) {
        try {
          const TDetail = await axios('/testticketordergroupby2/' + this.ticket_id + '/')
          // console.log(TDetail.data)

          subCList.data.forEach((sub) => {
            sub.foods = []
            DList.data.forEach((dish) => {
              if (dish.dsubcategory === sub.subcategory_id) {
                sub.foods.push(dish)
                // console.log(sub.foods.indexOf(dish))
                // console.log(sub.foods)
                TDetail.data.forEach((selectedDish) => {
                  // console.log(selectedDish.dname)
                  // console.log(dish.dname)
                  if (selectedDish.dname === dish.dname) {
                    Vue.set(sub.foods[sub.foods.indexOf(dish)], 'selected', selectedDish.total_quantity[0])
                  }
                })
                // const i = DList.data.findIndex(d => d.dname === dish.dname)
                // DList.data.splice(i, 1) //想用来去重，未成功，待研究
              }
            })
          })
          // console.log(subCList.data)
          this.foodList = subCList.data // 获取到改装后的数组
          this.ticketDetail = TDetail
        } catch (err) {
          console.log(err)
        }
      }

      // 未category和dish增加滚动效果
      this.$nextTick(() => {
        if (!this.categoryScroll) {
          this._initScroll()
          this._calculateHeight()
          // console.log('创建了新的Bs')
          // alert('创建了新的Bs')
        } else {
          this.categoryScroll.refresh()
          // console.log('刷新了Bs')
        }
      })
    },
    selectFood (index) {
      console.log(index)
      const foodList = this.$refs.dishWrapper.getElementsByClassName('category-list-hook')
      const el = foodList[index]
      this.dishScroll.scrollToElement(el, 300)
    },
    _initScroll () {
      // CategoryContainer滚动
      this.categoryScroll = new BScroll('.categoryContainer', {
        scrollY: true,
        click: true
      })
      // console.log(this.categoryScroll)
      // DishContainer滚动
      this.dishScroll = new BScroll('.dishContainer', {
        scrollY: true,
        click: true,
        probeType: 3
      })
      // console.log(this.dishScroll)

      this.dishScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y))
        // console.log(this.scrollY)
      })
    },
    _calculateHeight () {
      const foodList = this.$refs.dishWrapper.getElementsByClassName('category-list-hook')
      // console.log(this.categoryScroll)
      // console.log(foodList)
      let height = 0
      this.listHeight.push(height)
      foodList.forEach((item) => {
        height += item.clientHeight - 5
        this.listHeight.push(height)
      })
      // console.log(this.listHeight)
    },
    submit () {
      // getSubCategory(this)
      submitOrder(this)
    },
    async submitWechatOrder (ticketDetail) {
      // console.log(ticketDetail)
      console.log('开始新建order::::::::')
      const pattern = /\[(.*?)\]/
      ticketDetail.orders.forEach(order => {
        if (ticketDetail) {
          var date = new Date()
          var orderTime = date.getFullYear().toString() +
                          (((date.getMonth() + 1).toString().length < 2) ? '0' + (date.getMonth() + 1).toString() : (date.getMonth() + 1).toString()) +
                          date.getDate().toString() +
                          (((date.getHours()).toString().length < 2) ? '0' + date.getHours().toString() : date.getHours().toString()) +
                          (((date.getMinutes()).toString().length < 2) ? '0' + date.getMinutes().toString() : date.getMinutes().toString()) +
                          (((date.getSeconds()).toString().length < 2) ? '0' + date.getSeconds().toString() : date.getSeconds().toString())
          const orderData = {}
          Vue.set(orderData, 'order_id', 'D' + date.getTime().toString() + Math.floor(Math.random() * (999 - 100 + 1) + 100).toString())
          Vue.set(orderData, 'order_time', orderTime)
          Vue.set(orderData, 'quantity', order.dishCount)
          // console.log('测试dish code', pattern.exec(order.dishName)[1])
          // const code = pattern.exec(order.dishName)[1]
          const code = order.dishName.match(pattern)
          // console.log(code[1])
          Vue.set(orderData, 'code', code[1])
          Vue.set(orderData, 'tid', this.ticket_id)
          Vue.set(orderData, 'o_note', '无备注')
          console.log(JSON.stringify(orderData))
          // console.log(orderData)
          try {
            // console.log(orderData)
            axiosCreat(orderData, this)
          } catch (err) {
            console.log(err)
          }
        }
      })
      setTimeout(() => {
        getSubCategory(this)
        this.$parent.getTicketOrder()
        // this.$parent.patchTicketPrice()
      }, 300)
    }
  },
  beforeDestroy () {
    this.categoryScroll = null
    this.dishScroll = null
  },
  watch: {
    'this.scrollY': function () {
      console.log('currentIndex变动了')
    },
    deep: true,
    immediate: true
  }
}
</script>

<style lang="scss" scoped>
  .outWrapper {
    // width: 100%;
    height: calc(100vh - 80px - 70px - 115px); //80px:topNav, 70px:bottomnav, 115px: ios特有
    box-sizing: border-box;
    // border: 1px solid black;
    display: grid;
    grid-template-columns: 2fr 5fr;
    overflow: hidden;
    .categoryContainer {
      // border: 1px solid black;
      // height: 100%;
      overflow: hidden;
      ul {
        li {
          height: 40px;
          display: flex;
          align-items: center;
          justify-content: center;
          span {
            width: 90%;
            height: 100%;
            line-height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            // border-bottom: 1px solid rgb(219, 219, 219);
            border-bottom: 1px solid rgba(7, 17, 27, 0.1);
          }
        }
        .categorySelect {
          color: rgb(0, 185, 0);
          border-left: 2px solid rgb(0, 185, 0);
          background-color: white;
          font-weight: 700;
        }
        .categoryNoselect {
          // background-color: rgb(245, 245, 245);
          background-color: #f3f5f7;
        }
      }
    }

    .dishContainer {
      // border: 1px solid black;
      // height: 100%;
      overflow: hidden;
      .title {
        background-color: #f3f5f7;
        color: rgb(147, 153, 159);
        text-align: left;
        font-size: 12px;
        font-weight: 500;
        padding-left: 10px;
        height: 26px;
        display: grid;
        align-items: center;
      }
      .foodList {
        background-color: white;
        padding: 10px 5px;
        margin-left: 5px;
        border-bottom: 1px solid rgb(219, 219, 219);
        // min-height: 80px;
        min-height: 80px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        .firstline {
          width: 95%;
          // border: 1px solid black;
          display: flex;
          justify-content: flex-start;
          align-items: center;

          p:nth-child(1) {
            // color: red;
            flex: 9;
            text-align: start;
          }
          p:nth-child(2) {
            flex: 1;
            font-size: 0.5em;
          }
        }

        .secondline {
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;

          p {
            margin-top: 5px;
            flex: 2;
            text-align: start;
          }

          div {
            display: flex;
            justify-content: flex-end;
            place-items: 20px;
            flex: 3;
          }

          .quantity {
            display: grid;
            grid-template-columns: repeat(4,1fr);
            align-items: center;
            .add {
              box-sizing: content-box;
              width: 2em;
              height: 2em;
              border: 10px solid transparent;
              border-radius: 2em;
              background-clip: padding-box;
              // border: 1px solid #3ada3a;
              background-color: rgba($color: #217969, $alpha: 0.8);
              color: #fff;
              // line-height: 2em;
              text-align: center;
              grid-column: 4/5;
            }
            .dish_quantity {
              margin-right: 1em;
              text-align: center;
              margin: 0 auto;
              grid-column: 3/4
            }
            .minus{
              box-sizing: content-box;
              // margin-right: 1em;
              width: 1.9em;
              height: 1.9em;
              // border: 10px solid transparent;
              border-radius: 2em;
              background-clip: padding-box;
              border: 1px solid rgba($color: #217969, $alpha: 0.8);
              background-color: #fff;
              color: #217969;
              text-align: center;
              grid-column: 2/3
            }
          }
        }
      }
    }
  }
</style>
