<template>
  <div class="outer2">
    <div class="header">
      <i class="iconfont icon-zuojiantou"
      style="text-align: right; margin-left: 0px; border: 2px solid white; border-radius: 50%; font-size:2em; color: black; font-weight: bolder"
      @click="handleShow">
      </i>
      <p>桌号：{{ ticketDetail.table_num }}</p>
    </div>

    <div class="stickyArea">
      <div id='total'>
        <div id="tax10">
          <p class="taxTitle">10%</p>
          <p>{{ tatalTax10 }}</p>
        </div>
        <div id="tax20">
          <p class="taxTitle">20%</p>
          {{ tatalTax20 }}
        </div>
        <div id="totalTax">
          <p class="taxTitle">总TVA</p>
          {{ tatalTax }}</div>
        <h2 id='totalPrice'>总：{{ ticketDetail.ticket_price }}</h2>
      </div>
      <div class="detailArea">
          <div class="detail" style="color: #2CA4B0">
            <p class="title">转账</p>
            <p class="amount">{{ ticketDetail.pay_by_transfer }}</p>
          </div>
          <div class="detail" style="color: #ff4a4a">
            <p class="title">现金</p>
            <p class="amount">{{ ticketDetail.pay_by_cash }}</p>
          </div>
          <div class="detail" style="color: #217969">
            <p class="title">饭票</p>
            <p class="amount">{{ ticketDetail.pay_by_ticket }}</p>
          </div>
          <div class="detail" style="color: #b48608">
            <p class="title">刷卡</p>
            <p class="amount">{{ ticketDetail.pay_by_card }}</p>
          </div>
          <div class="detail">
            <p class="title">支票</p>
            <p class="amount">{{ ticketDetail.pay_by_check }}</p>
          </div>
          <div class="detail">
            <p class="title">微信</p>
            <p class="amount">{{ ticketDetail.pay_by_wechat }}</p>
          </div>
          <div class="detail">
            <p class="title">阿里</p>
            <p class="amount">{{ ticketDetail.pay_by_alipay }}</p>
          </div>
      </div>
      <!-- <div class="pay">
        <div class="totalPay">已付：</div>
        <div class="restPay">剩余：</div>
      </div> -->
    </div>

    <div class="handleArea">
      <div class="handlePay">
        <div class="payAmount">
          <h3>金额：</h3>
          <input ref="inputArea" type="number" @focus="focus($event)" style="width: 90px; height: 40px; text-align: left; border: 1px solid rgba(7, 17, 27, 0.1)" v-model="inputAmount">
          <div class="totalPay">已付：{{ alreadyPay }}</div>
          <div class="restPay">剩余：{{ restPay }}</div>
        </div>
        <div class="payButton">
          <v-btn @click="handlePayByVirement">转账</v-btn>
          <v-btn @click="handlePayByCash">现金</v-btn>
          <div class="history" style='grid-row:1/6; grid-column:3/4; background-color: #eee; border-radius:10px'>
            <p>付款记录</p>
          </div>
          <v-btn @click="handlePayByTicket">饭票</v-btn>
          <v-btn @click="handlePayByCard">刷卡</v-btn>
          <v-btn @click="handlePayByCheck">支票</v-btn>
          <v-btn @click="handlePayByWechat">微信</v-btn>
          <v-btn @click="handlePayByAlipay">阿里</v-btn>
        </div>
      </div>
    </div>

    <div class='ordered_list' ref="orderWrapper">
      <ul>
        <li class="order_container" v-for="data in selectedDish" :key="data.order_id">
          <h3 class="dish_name">{{ Object.keys(data)[0] }}</h3>
          <p class="price">{{ Object.values(data)[0].price }}</p>
          <p class="quantity">{{ Object.values(data)[0].quantity }}份</p>
          <p class="subTotal">{{ (Object.values(data)[0].price * Object.values(data)[0].quantity).toFixed(2) }}</p>
        </li>
      </ul>
    </div>

    <div class="buttonArea">
        <v-btn dark rounded color="#217969" width="80%" @click="handleSubmit">
          <!-- <v-icon>mdi-pencil</v-icon> -->
          <span>提交</span>
        </v-btn>
        <v-btn dark rounded color="#ff4a4a" @click="backToFront" width="80%">退回前台</v-btn>
        <v-btn dark rounded color="#ff4a4a" @click="showPopup" width="80%">删除</v-btn>
    </div>

    <!-- <van-cell is-link @click="showPopup">展示弹出层</van-cell> -->
    <van-popup class="confirmArea" v-model="show" id='delete_submit' round position="bottom" :style="{ height: '30%', width: '100%'}">
      <v-btn dark rounded color="#ff4a4a" width="50%" @click="handleDelete">删除</v-btn>
      <v-btn rounded width="50%" @click="show = false">取消</v-btn>
    </van-popup>
  </div>
</template>

<script>
import axios from 'axios'
import BScroll from 'better-scroll'
import { VBtn } from 'vuetify/lib/components'
import 'vuetify/dist/vuetify.min.css'
// import 'material-design-icons-iconfont/dist/material-design-icons.css'
// import 'material'
import Vue from 'vue'
import { Popup } from 'vant'

Vue.use(Popup)
// Vue.use(VIcon, {
//   icons: {
//     iconfont: 'md'
//   }
// })

export default {
  props: ['detailShow', 'ticketId'],
  components: {
    // BScroll,
    VBtn
    // VIcon
  },
  data () {
    return {
      inputAmount: '',
      ticketDetail: {},
      selectedDish: [],
      payCondition: {},
      show: false
    }
  },
  mounted () {
    // this.$nextTick(() => {
    //   console.log('Mounted中绑定的')
    //   this._initScroll()
    // })
  },
  computed: {
    totalPrice () {
      // console.log('test:::::::::', this.$store.state.selectedDish)
      let total = 0
      if (this.selectedDish.length !== undefined) {
        for (const data of this.selectedDish) {
          // console.log(data)
          total += Object.values(data)[0].price * Object.values(data)[0].quantity
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax10 () {
      // console.log('测试：：：：：：：：：：：', this.$store.state.selectedDish.length)
      let total = 0
      if (this.selectedDish.length !== undefined) {
        for (const data of this.selectedDish) {
          if (Object.values(data)[0].tax === '0.10') {
            total += (Object.values(data)[0].price - (Object.values(data)[0].price / (1 + parseFloat(Object.values(data)[0].tax)))) * Object.values(data)[0].quantity
          }
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax20 () {
      let total = 0
      if (this.selectedDish.length !== undefined) {
        for (const data of this.selectedDish) {
          if (Object.values(data)[0].tax === '0.20') {
            total += Object.values(data)[0].tax * Object.values(data)[0].quantity
          }
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax () {
      return (parseFloat(this.tatalTax10) + parseFloat(this.tatalTax20)).toFixed(2)
    },
    alreadyPay () {
      let total = 0
      Object.keys(this.ticketDetail).forEach(item => {
        if (item.includes('pay_by')) {
          total += parseFloat(this.ticketDetail[item])
          // console.log(total)
        }
      })
      return total
    },
    restPay () {
      const total = this.totalPrice - this.alreadyPay
      return total.toFixed(2)
    }
  },
  methods: {
    handleShow () {
      this.orderScroll = null
      // console.log(this.orderScroll)
      this.$emit('hidedetail')
    },
    focus (event) {
      event.currentTarget.select()
    },
    handlePayClick () {
      this.payingList = []
      this.$store.commit('changePayShow')
    },
    _initScroll () {
      this.orderScroll = new BScroll('.ordered_list', {
        scrollY: true,
        click: true
      })

      // 获取点击的dom元素
      // const dish = this.$refs.orderWrapper
      // dish.addEventListener('click', (evt) => {
      //   console.log(evt.target.parentNode)
      // })
    },
    // 转账支付
    handlePayByVirement () {
      // pay('pay_by_transfer', this.$store.state.payCondition.virement, this)
      if (this.inputAmount !== '' && this.inputAmount >= 0) {
        Vue.set(this.ticketDetail, 'pay_by_transfer', parseFloat(this.inputAmount).toFixed(2))
      }
    },
    // 现金支付
    handlePayByCash () {
      // pay('pay_by_cash', this.$store.state.payCondition.cash, this)
      if (this.inputAmount !== '' && this.inputAmount >= 0) {
        Vue.set(this.ticketDetail, 'pay_by_cash', parseFloat(this.inputAmount).toFixed(2))
      }
    },
    handlePayByTicket () {
      // pay('pay_by_ticket', this.$store.state.payCondition.ticket, this)
      if (this.inputAmount !== '' && this.inputAmount >= 0) {
        Vue.set(this.ticketDetail, 'pay_by_ticket', parseFloat(this.inputAmount).toFixed(2))
      }
    },
    handlePayByCard () {
      // pay('pay_by_card', this.$store.state.payCondition.card, this)
      if (this.inputAmount !== '' && this.inputAmount >= 0) {
        Vue.set(this.ticketDetail, 'pay_by_card', parseFloat(this.inputAmount).toFixed(2))
      }
    },
    handlePayByCheck () {
      // pay('pay_by_check', this.$store.state.payCondition.check, this)
      if (this.inputAmount !== '' && this.inputAmount >= 0) {
        Vue.set(this.ticketDetail, 'pay_by_check', parseFloat(this.inputAmount).toFixed(2))
      }
    },
    handlePayByWechat () {
      // pay('pay_by_wechat', this.$store.state.payCondition.wechat, this)
      if (this.inputAmount !== '' && this.inputAmount >= 0) {
        Vue.set(this.ticketDetail, 'pay_by_wechat', parseFloat(this.inputAmount).toFixed(2))
      }
    },
    handlePayByAlipay () {
      // pay('pay_by_alipay', this.$store.state.payCondition.alipay, this)
      if (this.inputAmount !== '' && this.inputAmount >= 0) {
        Vue.set(this.ticketDetail, 'pay_by_alipay', parseFloat(this.inputAmount).toFixed(2))
      }
    },
    handleSubmit () {
      if (this.restPay === '0.00') {
        axios({
          url: `/testticketdetailpaied/${this.ticketId}/`,
          method: 'PATCH',
          data: this.ticketDetail
        }).then((res) => { alert('修改成功') }).catch((err) => console.log(err))
      } else {
        alert(`剩余${this.restPay}未结清`)
      }
    },
    showPopup () {
      this.show = true
    },

    backToFront () {
      axios({
        url: `/testticketdetailpaied/${this.ticketId}/`,
        method: 'PATCH',
        data: { payment_status: 0 }
      }).then((res) => {
        // alert('删除成功')
        // this.$router.push('/delivery')
        this.$router.go(0)
      }).catch((err) => console.log(err))
    },

    handleDelete () {
      axios({
        url: `/testticketdetailpaied/${this.ticketId}/`,
        method: 'DELETE'
      }).then((res) => {
        // alert('删除成功')
        // this.$router.push('/delivery')
        this.$router.go(0)
      }).catch((err) => console.log(err))
    }
  },
  watch: {
    'detailShow' () {
      axios({
        url: `/testticketdetailpaied/${this.ticketId}/`
      }).then((res) => { this.ticketDetail = res.data }).catch((err) => console.log(err))

      axios({
        url: `/testticketordergroupby2/${this.ticketId}/`
      }).then(res => {
        console.log(res.data)
        this.selectedDish = res.data.map(function (item) {
          var Obj = {}
          // Obj[item.dname] = item.total_quantity[0]
          Obj[item.dname] = { quantity: item.total_quantity[0], price: item.dprice, tax: item.dtax }
          return Obj
        })
        this.$nextTick(() => {
          if (this.detailShow) {
            if (!this.orderScroll) {
              this._initScroll()
              // console.log('生成了新的Bs')
            } else {
              this.orderScroll.refresh()
              // console.log('刷新了Bs')
            }
            // console.log('detail为true')
          }
        })
      }).catch((err) => console.log(err))
    }
  }
}

</script>

<style lang="scss" scoped>
  .outer2 {
    position: fixed;
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    top: 0px;
    z-index: 10;
    padding: 0 0px;
    background-color: #fff;
    overflow-y: auto;
    .header {
      position: fixed;
      top: 0px;
      width: 100%;
      background-color: #fff;
      display: grid;
      grid-template-columns: 1fr 2fr 8fr;
      align-items: center;
      // justify-content: space-evenly;
      .icon-shangjiantou1 {
      transform: rotate(180deg);
      // flex: 0 0 60px;
      }
    }
    .stickyArea {
      position: relative;
      top: 35px;
      height: 100px;
      background-color: #fff;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      width: 100%;
      box-sizing: border-box;
      // border: 1px solid black;
      margin: 0;
      #total {
        padding: 5px 10px;
        width: 100%;
        height: 42px;
        display: flex;
        align-items: center;
        // border-bottom: 1px solid #eee;
        // flex-direction: row-reverse;
        .taxTitle {
          font-size: 10px;
        }
        #tax10 {
          flex: 1;
          font-size: 14px;
        }
        #tax20 {
          flex: 1;
          font-size: 14px;
        }
        #totalTax {
          flex: 1;
          font-size: 14px;
        }
        #totalPrice {
          flex: 0 0 130px;
          text-align: end;
        }
      }
      .detailArea {
        // position: sticky;
        height: 50px;
        display: grid;
        grid-template-columns:repeat(7, 1fr);
        grid-column-gap: 0.5em;
        grid-row-gap: 0.2em;
        // border: 1px solid black;
        .detail {
          height: 50px;
          // border: 1px solid black;
        }
      }
      .pay {
        display: flex;
        justify-content: flex-end;
        font-weight: 600;
        .restPay {
          flex: 0 0 130px;
          text-align: end;
        }
      }
    }

    .handleArea {
      position: relative;
      top: 35px;
      margin-top: 5px;
      padding: 5px;
      display: grid;
      grid-template-columns: 1fr;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      // justify-content: space-between;
      .handlePay {
        // border: 1px solid black;
        display: flex;
        flex-direction: column;
        .payAmount {
          display: grid;
          grid-template-columns: 2fr 2fr 3fr 3fr;
          align-items: center;
          // flex-direction: column;
        }
        .payButton {
          margin-top: 10px;
          display: grid;
          grid-template-columns: 1fr 1fr 1fr;
          grid-gap: 1em;
          z-index: 1;
        }
      }
      .history {
        flex: 1;
      }
    }

    .ordered_list {
      position: relative;
      top: 35px;
      height: 230px;
      z-index: 10;
      overflow: hidden;
      // overflow-y: auto;
      // border: 2px solid black;
      // background-color: red;
      box-sizing: border-box;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      padding: 10px;
      .order_container{
          display: grid;
          grid-template-columns: 2fr 1fr 1fr 1fr;
          width: 100%;
          align-items: center;
          grid-auto-rows: 2em;
            .dish_name{
              text-align: start;
              white-space:nowrap;
              text-overflow:ellipsis;
              overflow:hidden;
            }
            .price {
              text-align: start;
            }
            .quantity{
              font-size: 1em;
              text-align: start;
            }
            .subTotal {
              text-align: end;
            }
      }
    }

    .buttonArea {
      position: fixed;
      width: 100%;
      // top: 35px;
      bottom: 25px;
      // border: 1px solid black;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      justify-items: center;
      // justify-content: center;
    }

    .confirmArea {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      justify-items: center;
      align-items: center;
    }
  }
</style>
