<template>
  <div class="outer">
    <!-- <iframe :src="webSrc"  frameborder="1" id="xinweiyun"></iframe> -->
    <!-- <v-btn @click="getCookie">获取cookie</v-btn> -->
    <div class="top">
      <i class="iconfont icon-zuojiantou" @click="$emit('hideWechat')"></i>
      <v-btn @click="getData">刷新数据</v-btn>
    </div>
    <div style="width=100%, height=100px">
      <ul class="listContainer">
        <li v-for="item in info" :key="item.ticketNumber" @click="handleClick(item)">
          <div class="ticketContainer">
            <v-btn small rounded @click.stop="handleCrawl(item)">调取</v-btn>
            <p>{{item.ticketType}}</p>
            <p>{{item.ticketNumber}}</p>
            <p>{{item.clientName}}</p>
            <p style="font-size:14px">{{item.deliveryTime}}</p>
          </div>
          <!-- {{item.href}} -->
        </li>
      </ul>
    </div>
    <transition name="detail">
      <wechat-detail v-show="detailShow" :ticket='ticket'></wechat-detail>
    </transition>
  </div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'
import WechatDetail from './WechatDetail'
import { Toast } from 'vant'

export default {
  components: {
    WechatDetail
  },
  data: () => ({
    detailShow: false,
    webSrc: 'http://www.xinweiyun.com/weixin/index.php/admin/wapindex.jtml?' + '?' + Math.random(),
    webSrc2: 'http://www.newsavour.com/restaurants/',
    info: [],
    ticket: {},
    direction: 'top',
    fab: false,
    fling: false,
    hover: false,
    tabs: null,
    top: false,
    right: true,
    bottom: true,
    left: false,
    transition: 'slide-y-reverse-transition'
  }),

  created () {
    axios.interceptors.request.use((config) => {
      Toast.loading({
        message: '加载中...',
        forbidClick: true,
        overlay: false,
        closeOnClickOverlay: true,
        loadingType: 'spinner',
        duration: 5000
      })
      return config
    }, (err) => {
      return Promise.reject(err)
    })

    axios.interceptors.response.use((response) => {
      Toast.clear()
      return response
    }, (err) => {
      return Promise.reject(err)
    })
  },

  mounted () {
    this.getData()
  },

  computed: {
    activeFab () {
      switch (this.tabs) {
        case 'one': return { class: 'purple', icon: 'account_circle' }
        case 'two': return { class: 'red', icon: 'edit' }
        case 'three': return { class: 'green', icon: 'keyboard_arrow_up' }
        default: return {}
      }
    }
  },

  methods: {
    getCookie () {
      console.log(document.cookie)
    },
    handleDetailShow () {
      this.detailShow = !this.detailShow
    },
    getData () {
      this.info = []
      axios({
        url: '/testAxios2/'
      }).then(res => {
        // console.log(Object.values(res)[0])
        // const pattern = /<A class="color364 displayordera.*?jtml">(.*?)<\/A>.*?col_12"><span>(\d+\.\d+).*?col_13.*?(\d{7}).*?(\d+-\d+-\d+ \d+:\d+:\d+).*?(\d{1,2}月.*?-\s?\d+:\d{2}).*?col_8"><span>(.*?)<\/span><\/li><li class="col_11.*?<div class="orderclone">/gm // \为转义符
        const pattern = /<A class="color364 displayordera.*?jtml">(.*?)<\/A>.*?(\d{7}).*?col_12"><span>(\d+\.\d+).*?col_9"><span>(.*?)-货到付款.*?(\d+-\d+-\d+ \d+:\d+:\d+)<\/span><\/li><li class="col_8"><span>(.*?)<\/span>.*?col_8"><span>(.*?)<\/span><\/li><li class="col_11.*?<div class="orderclone">/g // \为转义符
        // 捕获1 - (.*?)：客户昵称
        // 捕获2 - (\d{7}) ：7位数字，匹配订单号
        // 捕获3 - (\d+\.\d+)：匹配订单价格
        // 捕获4 - >(.*?)-货到付款：匹配“外送”或者“自提”
        // 捕获5 - (\d+-\d+-\d+ \d+:\d+:\d+)：匹配下单时间
        // 捕获6 - (\d{1,2}月.*?-\s?\d+:\d{2})：\s?表示可能有空格（也可能无空格）

        // const response = (Object.values(Object.values(res)[0][0])[0]).match(pattern) // 对应 --> '/testAxios/'
        // const response = Object.values(res)[0].match(pattern) // 对应 --> '/testAxios2/'
        // let response = pattern.exec(Object.values(res)[0])
        // console.log(response)
        let ticket = {}
        for (let i = 0; i < 20; i++) {
          const response = pattern.exec(Object.values(res)[0]) // 对应 --> '/testAxios2/'
          // console.log(response)
          ticket = {}
          Vue.set(ticket, 'clientName', response[1])
          Vue.set(ticket, 'ticketNumber', response[2])
          Vue.set(ticket, 'ticketPrice', response[3])
          Vue.set(ticket, 'ticketType', response[4])
          Vue.set(ticket, 'orderTime', response[5])
          Vue.set(ticket, 'deliveryTime', response[6])
          Vue.set(ticket, 'ticketNote', response[7])
          this.info.push(ticket)
          // console.log(ticket)
        }
        // console.log(this.info)

        // for (const index in response) {
        //   const ticket = {}
        //   // 获取订单网址(后发现不需要了，订单网址是由一串固定 字符串+单号 拼接而成的)
        //   // let patt = /\/weixin.*?jtml/
        //   // const href = response[index].match(patt)
        //   // Vue.set(ticket, 'href', href[0])

        //   // 获取订单号
        //   const patt = /\d{7}/
        //   const ticketNumber = response[index].match(patt)
        //   Vue.set(ticket, 'ticketNumber', ticketNumber[0])

        //   this.info.push(ticket)
        //   // console.log(ticket)
        // }
      })
    },
    async getDetail (item) {
      const order = []
      return axios(`/testAxios/${item.ticketNumber}/`).then(res => {
        // const detail = {}
        // console.log(res.data)
        // 获取订单：如数量、菜名、单价等
        const pattern = /new_table new_table_chlid.*?<span title="(.*?)">.*?id="sl" value="(.*?)".*?id="jg" value="(.*?)".*?nbsp;分/g
        const response = Object.values(res.data[0])[0].match(pattern)
        // console.log(response.length)
        // console.log(response)
        let originTotal = 0
        for (let i = 0; i < response.length; i++) {
          const response11 = pattern.exec(Object.values(res.data[0])[0]) // 对应 --> '/testAxios2/'
          // Vue.set(detail, 'dishName', response11[1])
          // Vue.set(detail, 'dishCount', response11[2])
          // Vue.set(detail, 'dishPrice', response11[3])
          order.push({ dishName: response11[1], dishCount: response11[2], dishPrice: response11[3] })
          originTotal += response11[2] * response11[3]
        }
        console.log(originTotal)
        Vue.set(this.ticket, 'orders', order)

        // 获取基本信息：如地址、电话等
        //    - 如果是外送的单子，则页面上有邮编zipcode
        //    - 如果是到店自提的单子，则页面上没有邮编
        // console.log('ticketType::::::::::', item.ticketType)
        if (item.ticketType === '外卖') {
          const pattern2 = /id="psdz" value="(.*?\n?.*?)".*?name="customerZipCodeNum".*?value="(.*?)".*?name="tel".*?value="(.*?)".*?/g
          const response2 = pattern2.exec(Object.values(res.data[0])[0])
          // const pattTest = /id="psdz" value="(.*?\n?.*?)".*?/g
          // const resTest = pattTest.exec(Object.values(res.data[0])[0])
          // console.log(Object.values(res.data[0])[0])
          // console.log(response2)
          // console.log(resTest)
          let address = response2[1].split('起送')[1]
          console.log(address)
          address = address.replace(/\n/g, ' ')
          const testAddress = /^.*?[市|岛|区|街]?.*[市|岛|区|街](.*?)$/g.exec(address)
          if (testAddress) { address = testAddress[1] }
          // console.log(testAddress.length)
          Vue.set(this.ticket, 'address', address + ' ' + response2[2])
          Vue.set(this.ticket, 'zipCode', response2[2])
          Vue.set(this.ticket, 'tel', response2[3])
          Vue.set(this.ticket, 'type', item.ticketType)
          Vue.set(this.ticket, 'clientName', item.clientName)
          Vue.set(this.ticket, 'ticketNumber', item.ticketNumber)
          Vue.set(this.ticket, 'deliveryTime', item.deliveryTime)
          Vue.set(this.ticket, 'orderTime', item.orderTime)
          Vue.set(this.ticket, 'ticketNote', item.ticketNote)
          switch (true) {
            case (originTotal > 100):
              Vue.set(this.ticket, 'reduction', 8)
              break
            case (originTotal > 75):
              Vue.set(this.ticket, 'reduction', 5)
              break
            case (originTotal > 50):
              Vue.set(this.ticket, 'reduction', 3)
              break
            default:
              Vue.set(this.ticket, 'reduction', 0)
              break
          }
          Vue.set(this.ticket, 'ticketPrice', (originTotal - this.ticket.reduction))
        } else {
          const pattern2 = /id="psdz" value="(.*?)".*?name="tel".*?value="(.*?)".*?/g
          const response2 = pattern2.exec(Object.values(res.data[0])[0])
          const adress = response2[1].split('起送')[1]
          Vue.set(this.ticket, 'address', adress)
          Vue.set(this.ticket, 'tel', response2[2])
          Vue.set(this.ticket, 'type', item.ticketType)
          Vue.set(this.ticket, 'clientName', item.clientName)
          Vue.set(this.ticket, 'ticketNumber', item.ticketNumber)
          Vue.set(this.ticket, 'deliveryTime', item.deliveryTime)
          Vue.set(this.ticket, 'orderTime', item.orderTime)
          Vue.set(this.ticket, 'ticketNote', item.ticketNote)
        }

        // console.log('内部：：：：', this.ticket)
      })
    },
    handleClick (item) {
      this.handleDetailShow()
      this.getDetail(item)
    },

    // “调取”按钮：爬取订单并creatWechatTicket
    async handleCrawl (item) {
      await this.getDetail(item)
      this.detailShow = false
      this.$emit('createNewWechatTicket', this.ticket)
      this.$emit('hideWechat')
    }
  },

  watch: {
  }
}
</script>

<style lang="scss" scoped>
  .outer{
    position: fixed;
    top: 0;
    width: 100%;
    height: calc(100vh - 165px);
    overflow: hidden;
    overflow-y:auto;
    z-index: 10;
    background-color: #fff;
    // border: black 1px solid;
    .top {
      display: grid;
      justify-content: start;
      align-items: center;
      grid-template-columns: 30px 1fr;
      button {
        // border: 1px solid black;
        justify-self: center;
      }
    }
    #xinweiyun {
      width: 100%;
      height: 400px;
    }
    .listContainer {
      margin-top: 10px;
      width: 100%;
      height: calc(100vh - 235px);
      overflow: hidden;
      overflow-y:auto;
      // border: 1px solid black;
      .ticketContainer {
        // width: 100%;
        height: 50px;
        display: grid;
        // grid-template-columns: 100px 100px 200px;
        grid-template-columns: 1fr 2fr 4fr 3fr 8fr;
        justify-content: start;
        align-items: center;
        // grid-gap: 5px;
        // border: 1px solid red;
      }
    }
    li:nth-child(odd){
      background: #eee;
    }
    .container {
      position: fixed;
      width: 200px;
      height: 200px;
      bottom: 80px;
      right: 10px;
      border: red 1px solid;
      padding: 0;
      .btn {
        position: absolute;
        margin: 0;
        border: green 1px solid;
        width: auto;
        bottom: 0;
        right: 0px;
        // height: 100px;
      }
    }

    .detail-enter-active {
      animation: fadeFromBottom 0.2s;
    }

    .detail-leave-active {
      animation: fadeFromBottom 0.2s reverse;
    }

    @keyframes fadeFromBottom {
      0% {
        transform: translateX(100%);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
</style>
