<template>
  <div class="nav">
    <div class="nav_icon">
      <i class="iconfont icon-gouwuche"
     style="margin-left: 15px; border: border-radius: 50%; font-size:2em; color: rgba(7, 17, 27, 0.5); font-weight: normal"
     @click="handleTicketClick"></i>
      <i class="iconfont icon-jiezhang"
     style="margin-left: 15px; border: 2px solid white; border-radius: 50%; font-size:2em; color:rgba(7, 17, 27, 0.5); font-weight: bolder"
     @click="handlePayClick"></i>
    </div>
    <h3>€{{ total }}</h3>
    <div class="confirm" @click="handleSubmit">
      <p>添加</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ['ticketDetail'],
  computed: {
    total () {
      let total = 0
      // console.log(this.$parent.$children[1].foodList)
      if (this.$parent.$children[1].foodList) {
        // console.log('foodList不为零', this.$parent.$children[1].foodList)
        this.$parent.$children[1].foodList.forEach(category => {
          category.foods.forEach(food => {
            if (food.selected) {
              total += food.dprice * food.selected
            }
            if (food.selecting) {
              total += food.dprice * food.selecting
            }
          })
        })
      } else {
        console.log('foodList为零')
      }
      return (total * this.ticketDetail.ticket_discount - this.ticketDetail.ticket_reduction).toFixed(2)
    }
  },
  methods: {
    handleSubmit () {
      this.$emit('submit')
    },
    handleTicketClick () {
      this.$emit('handelTicketShow')
    },
    handlePayClick () {
      this.$emit('handelPayShow')
    }
  }
}
</script>

<style lang="scss" scoped>
  .nav {
    border-top: 1px solid rgba(7, 17, 27, 0.1);
    position: fixed;
    bottom: 0px;
    background-color: #fff;
    /*background-color: #B2DFDB;*/
    // background-color: rgba(7, 17, 27, 0.1);
    width: 100%;
    height: 70px;
    z-index: 9;
    // box-shadow: 0px -1px #eee;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    // flex-direction: row-reverse;
    // justify-content: space-between;
    align-items: center;
    .confirm{
      grid-column: 4/5;
      // width: 20%;
      height: 100%;
      background-color: orangered;
      color: white;
      font-weight: bolder;
      text-align: center;
      justify-content: center;
      align-items: center;
      display: flex;
    }
    .nav_icon {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
    }
  }
</style>
