      console.log('进入Quickly')
      const patt = /\|/g // \为转义符
      const res = patt.exec(content) // 匹配到3个 | 符号
      console.log(res)
      if (res) {
        const contentList = content.split('|')
        // console.log(contentList)
        const time = contentList[0]
        const name = contentList[1]
        const address = contentList[2]
        const telphone = contentList[3]
        const ticketNote = contentList[4]
