    // 修改数据库中的ticket总价 - ticketPrice
    async patchTicketPrice () {
      console.log('开始patch价格', this.$refs.bottomnav.total)
      return axios({
        url: `/testticketdetailpaied/${this.ticket_id}/`,
        method: 'PATCH',
        data: { ticket_price: this.$refs.bottomnav.total }
      })
    },
    patchWechatTicketPrice () {
      // console.log(this.wechatTicket)

      // 获取“原总价”(orinalPrice)
      let price = 0
      this.wechatTicket.orders.forEach(order => {
        price += Number(order.dishCount) * Number(order.dishPrice)
      })

      // 满减优惠
      switch (true) {
        case (price > 100):
          price -= 8
          break
        case (price > 75):
          price -= 5
          break
        case (price > 50):
          price -= 3
          break
        default:
          break
      }
      // console.log(price)

      // 向ticketData中patch总价
      axios({
        url: `/testticketdetailpaied/${this.ticket_id}/`,
        method: 'PATCH',
        data: { ticket_price: price }
      }).then(res => console.log(res))
