<style lang="scss" scoped>
  .outer2 {
    position: fixed;
    box-sizing: border-box;
    width: 100%;
    height: 100vh;
    top: 0px;
    z-index: 10;
    padding: 0 0px;
    background-color: #fff;
    overflow-y: auto;
  }

  .ticket-enter-active {
    animation: fadeFromBottom 0.2s;
  }

  .ticket-leave-active {
    animation: fadeFromBottom 0.2s reverse;
  }

  @keyframes fadeFromBottom {
    0% {
      transform: translateY(100%);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
</style>
