<template>
  <div class="Print">
    <button class="print-btn" v-on:click="handleAxios">
      <span>{{msg}}</span>
    </button>
    <ul class="container">
      <li v-for="item in axiosData" :key="item">{{item}}</li>
    </ul>
  </div>
</template>
<script>
import { getLodop } from '../utils/LodopFuncs' // 导入模块
import axios from 'axios'
export default {
  name: 'Print',
  data () {
    return {
      msg: '点击按钮打印',
      axiosData: []
    }
  },
  methods: {
    btnClickPrint: function () {
      const LODOP = getLodop()// 调用getLodop获取LODOP对象
      LODOP.PRINT_INIT('')
      LODOP.ADD_PRINT_HTM(20, 2, 260, 39, '<h1>打印内容</h1>')
      LODOP.ADD_PRINT_RECT(10, 10, 200, 400, 0, 1)
      // LODOP.PREVIEW()
      LODOP.PRINT()
    },

    async getDishGroupby0 () {
      console.log('执行0')
      const res = await axios.get('/testdishordergroupby/0/')
      return res
    },

    getDishGroupby1 () {
      console.log('执行1')
      return axios.get('/testdishordergroupby/0/')
    },

    getDishGroupby2 () {
      console.log('执行2')
      return axios.get('/testdishordergroupby/0/')
    },

    async handleAxios () {
      this.axiosData = []
      console.log('开始')
      let res = await this.getDishGroupby0()
      this.axiosData.push(res.data[0].dname)
      res = await this.getDishGroupby1()
      this.axiosData.push(res.data[1].dname)
      res = await this.getDishGroupby2()
      this.axiosData.push(res.data[2].dname)
      console.log('结束')
      console.log(this.axiosData)
    }
  }
}
</script>
