<template>
  <div class="outer2">

    <div class="nav">
      <div class=category :class="[ activeTab === 3 ? 'active' : '']" @click="activeTab = 3"><p>全部</p></div>
      <div class=category :class="[ activeTab === 0 ? 'active' : '']" @click="activeTab = 0"><p>堂食</p></div>
      <div class=category :class="[ activeTab === 1 ? 'active' : '']" @click="activeTab = 1"><p>打包</p></div>
      <div class=category :class="[ activeTab === 2 ? 'active' : '']" @click="activeTab = 2"><p>外送</p></div>
    </div>

    <div class="searchBar">
       <i class="iconfont icon-fangdajing"></i>
       <div style="height: 90%; border-right: 1px solid rgba(7, 17, 27, 0.1)">
         <input type="text" placeholder="搜索内容" style="width: 100%; height: 100%; text-align:left; color: rgb(7, 17, 27)">
        </div>
       <div>
         过滤
         <i class="iconfont icon-arrow-right"></i>
       </div>
    </div>

    <Summary :ticketList='ticketList'></Summary>

    <ticket-list :ticketList='ticketList' @handleDetailShow='onHandleDetailShow'></ticket-list>

    <!-- <transition name=testslide>
      <arrangement :ticketList='ticketList' :arrangementShow='arrangementShow'
      v-show="arrangementShow" @hideArrangement='onHideArrangement'></arrangement>
    </transition> -->

    <!-- <div class="newTicket" @click="handleNewTicket">
      <i class="iconfont icon-plus"></i>
    </div> -->

    <v-speed-dial
      v-model="fab"
      transition='slide-y-reverse-transition'
      color='primary'
      class="newTicket"
    >
      <template v-slot:activator>
        <v-btn
          v-model="fab"
          color="#217969"
          dark
          fab
        >
          <v-icon v-if="fab">
            mdi-close
          </v-icon>
          <v-icon v-else>
            mdi-plus
          </v-icon>
        </v-btn>
      </template>
      <v-btn
        fab
        dark
        small
        color="#4CAF50"
        @click="wechatShow = !wechatShow"
      >
        <v-icon>mdi-wechat</v-icon>
      </v-btn>
      <v-btn
        fab
        dark
        small
        color="#3F51B5"
        @click="handleNewTicket"
      >
        <!-- <v-icon>mdi-card-plus-outline</v-icon> -->
        <!-- <p style="fontsize: 10px">外送</p> -->
        <i class="iconfont icon-qishou"></i>
      </v-btn>
      <v-btn
        fab
        dark
        small
        color="#7986CB"
        @click="handleNewEmporterTicket"
      >
        <!-- <p style="fontsize: 10px">打包</p> -->
        <i class="iconfont icon-waidai" style="fontsize:20px;"></i>
      </v-btn>
      <v-btn
        fab
        dark
        small
        color="#F44336"
        @click="getAllDishGroupBy"
      >
        <v-icon>mdi-function-variant</v-icon>
      </v-btn>
      <v-btn
        fab
        dark
        small
        color=""
        @click="handleArrangementShow"
      >
        <v-icon>mdi-account-details</v-icon>
        <!-- <p style="fontsize: 10px">调度</p> -->
      </v-btn>
    </v-speed-dial>

    <transition name=testslide>
      <delivery-detail ref="deliverydetail" :detailShow='detailShow' :ticket_id='ticketId'
      :wechatTicket='wechatTicket'
      v-show="detailShow" @hidedetail='onHideDetail'> </delivery-detail>
    </transition>

    <transition name=testslide>
      <wechat-list ref="wechatlist"
      v-show="wechatShow"
      @hideWechat='onHideWechat'
      @createNewWechatTicket='onCreateNewWechatTicket'></wechat-list>
    </transition>

    <transition name=testslide>
      <dish-groupby ref="groupbylist"
      :groupbyList='groupbyList'
      v-show="groupbyShow"
      @hideGroupby='onHideGroupby'
      @createNewWechatTicket='onCreateNewWechatTicket'></dish-groupby>
    </transition>

  </div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'
// import Detail from './Management/Detail'
import DeliveryDetail from './DeliveryOrder'
import BScroll from 'better-scroll'
import TicketList from '../components/TicketList.vue'
// import Arrangement from '../components/Arrangement'
import Summary from '../components/Summary'
import WechatList from '../components/WechatList'
import DishGroupby from './Delivery/DishGroupby.vue'

async function getTicket (_this) {
  const res = await axios({
    url: '/testticketlistnopaied/'
  })
  // console.log(res)handleDetailShow
  _this.ticketList = Object.values(res.data)
  _this.ticketList = _this.ticketList.filter((item) => item.type === 2)
  for (const item of _this.ticketList) {
    Vue.set(item, 'checked', false)
  }

  console.log(_this.ticketList)
  _this.$nextTick(() => {
    if (!_this.orderScroll) {
      _this._initScroll()
      // console.log('创建了新的Bs')
    } else {
      _this.orderScroll.refresh()
      // console.log('刷新了Bs')
    }
    // console.log('Bs已生效')
  })
}

export default {
  components: {
    DeliveryDetail,
    TicketList,
    Summary,
    WechatList,
    DishGroupby
    // Arrangement
  },
  data () {
    return {
      fab: false,
      activeTab: 2,
      ticketList: [],
      wechatTicket: {},
      detailShow: false, // 订单详情,下单页面
      wechatShow: false, // 小程序订单列表
      groupbyShow: false, // 菜品汇总页面
      arrangementShow: false,
      ticketId: '',
      groupbyList: [], // 全部订单菜品groupeby,用于厨房汇总单
      testshow: false
    }
  },
  mounted () {
    // const _this = this
    // console.log(this)
    getTicket(this)
    this.$store.commit('show') // 控制主页面底部tabbar显示
  },
  computed: {
    // 计算订单类别
    type () {
      return function (data) {
        if (data.type === 0) {
          return '堂食'
        } else if (data.type === 1) {
          return '打包'
        } else {
          return '外送'
        }
      }
    },
    // 计算订单标题
    title () {
      return function (data) {
        if (data.type === 0) {
          return '桌号：' + data.table_num
        } else if (data.type === 1) {
          return '打包'
        } else {
          return '编号：' + data.table_num
        }
      }
    },
    totalPrice () {
      // console.log('test:::::::::', this.$store.state.selectedDish)
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          // console.log(data)
          total += parseFloat(data.ticket_price)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax10 () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.ticket_tax10)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax20 () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.ticket_tax20)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax () {
      return (parseFloat(this.tatalTax10) + parseFloat(this.tatalTax20)).toFixed(2)
    },
    totalVirement () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_transfer)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalCash () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_cash)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalTicket () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_ticket)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalCard () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_card)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalCheck () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_check)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalWechat () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_wechat)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalAlipay () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_alipay)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    }
  },
  methods: {
    async getTicket () {
      const res = await axios({
        url: '/testticketlistnopaied/'
      })
      // console.log(res)
      if (this.activeTab === 3) {
        this.ticketList = Object.values(res.data)
      } else {
        this.ticketList = Object.values(res.data).filter(item => item.type === this.activeTab)
      }
      for (const item of this.ticketList) {
        Vue.set(item, 'checked', false)
      }

      // console.log(this.ticketList)
      this.$nextTick(() => {
        if (!this.orderScroll) {
          this._initScroll()
          console.log('创建了新的Bs')
        } else {
          this.orderScroll.refresh()
          console.log('刷新了Bs')
        }
      })
    },
    onHandleDetailShow (data) {
      console.log(data.ticket_id)
      this.ticketId = data.ticket_id
      setTimeout(() => { // 加setTimeOut是为了解决ticket_id传递到子组件的异步问题
        this.$refs.deliverydetail.$refs.orderarea.getSubCategory()
        this.$refs.deliverydetail.getTicketOrder()
        this.$refs.deliverydetail.getTicketBasicDetail()
      }, 0)
      this.detailShow = !this.detailShow
      // this.$router.push(`/delivery/${data.ticket_id}`)
    },
    onHideDetail (data) {
      this.getTicket()
      // this.activeTab = 2
      this.detailShow = false
    },
    onHideWechat () {
      this.getTicket()
      this.wechatShow = false
    },
    _initScroll () {
      this.orderScroll = new BScroll('.ticketList', {
        scrollY: true,
        click: true
      })
    },

    async countDeliveryTicket () {
      const res = await axios('/testticketlistlivraison/')
      return res.data.length
    },

    async countEmporterTicket () {
      const res = await axios('/testticketlistemporter/')
      return res.data.length
    },

    // 新增Livraison订单
    async createTicket () {
      const num = await this.countDeliveryTicket()
      // console.log(num)
      const ticketData = {}
      const tableNum = 'L' + (num + 1)
      const date = new Date()
      ticketData.ticket_id = 'T' + date.getTime().toString()
      ticketData.ticket_time = date.getFullYear().toString() +
                  (((date.getMonth() + 1).toString().length < 2) ? '0' + (date.getMonth() + 1).toString() : (date.getMonth() + 1).toString()) +
                  date.getDate().toString() +
                  (((date.getHours()).toString().length < 2) ? '0' + date.getHours().toString() : date.getHours().toString()) +
                  (((date.getMinutes()).toString().length < 2) ? '0' + date.getMinutes().toString() : date.getMinutes().toString()) +
                  (((date.getSeconds()).toString().length < 2) ? '0' + date.getSeconds().toString() : date.getSeconds().toString())
      ticketData.table_num = tableNum
      ticketData.type = 2
      this.ticketId = ticketData.ticket_id
      await axios({
        url: '/testcreatetable/',
        method: 'post',
        data: ticketData
      })
      return ticketData.ticket_id
    },

    // 新增Emporter订单
    async createEmporterTicket () {
      const num = await this.countEmporterTicket()
      // console.log(num)
      const ticketData = {}
      const tableNum = 'E' + (num + 1)
      const date = new Date()
      ticketData.ticket_id = 'T' + date.getTime().toString()
      ticketData.ticket_time = date.getFullYear().toString() +
                  (((date.getMonth() + 1).toString().length < 2) ? '0' + (date.getMonth() + 1).toString() : (date.getMonth() + 1).toString()) +
                  date.getDate().toString() +
                  (((date.getHours()).toString().length < 2) ? '0' + date.getHours().toString() : date.getHours().toString()) +
                  (((date.getMinutes()).toString().length < 2) ? '0' + date.getMinutes().toString() : date.getMinutes().toString()) +
                  (((date.getSeconds()).toString().length < 2) ? '0' + date.getSeconds().toString() : date.getSeconds().toString())
      ticketData.table_num = tableNum
      ticketData.type = 1
      this.ticketId = ticketData.ticket_id
      await axios({
        url: '/testcreatetable/',
        method: 'post',
        data: ticketData
      })
      return ticketData.ticket_id
    },

    async createWechatTicket (ticketDetail) {
      const num = await this.countDeliveryTicket()
      // console.log(num)
      const ticketData = {}
      const tableNum = 'L' + (num + 1)
      const date = new Date()
      ticketData.ticket_id = 'T' + date.getTime().toString()
      ticketData.ticket_time = date.getFullYear().toString() +
                  (((date.getMonth() + 1).toString().length < 2) ? '0' + (date.getMonth() + 1).toString() : (date.getMonth() + 1).toString()) +
                  date.getDate().toString() +
                  (((date.getHours()).toString().length < 2) ? '0' + date.getHours().toString() : date.getHours().toString()) +
                  (((date.getMinutes()).toString().length < 2) ? '0' + date.getMinutes().toString() : date.getMinutes().toString()) +
                  (((date.getSeconds()).toString().length < 2) ? '0' + date.getSeconds().toString() : date.getSeconds().toString())
      ticketData.table_num = tableNum
      ticketData.type = 2
      ticketData.delivery_platform = 5
      ticketData.delivery_time = ticketDetail.deliveryTime
      ticketData.client_name = ticketDetail.clientName
      ticketData.client_address = ticketDetail.address
      ticketData.client_telephone = ticketDetail.tel
      ticketData.ticket_note = ticketDetail.ticketNote
      ticketData.ticket_reduction = ticketDetail.reduction
      ticketData.ticket_price = ticketDetail.ticketPrice.toFixed(2)
      this.ticketId = ticketData.ticket_id
      return axios({
        url: '/testcreatetable/',
        method: 'post',
        data: ticketData
      })
      // return ticketData.ticket_id
    },

    async handleNewTicket () {
      this.wechatTicket = {}
      await this.createTicket()
      console.log(this)
      setTimeout(() => { // 加setTimeOut是为了解决ticket_id传递到子组件的异步问题
        console.log(this.$refs.deliverydetail)
        this.$refs.deliverydetail.$refs.orderarea.getSubCategory()
        this.$refs.deliverydetail.getTicketOrder()
        this.$refs.deliverydetail.getTicketBasicDetail()
      }, 0)
      this.detailShow = true
    },

    async handleNewEmporterTicket () {
      this.wechatTicket = {}
      await this.createEmporterTicket()
      console.log(this)
      setTimeout(() => { // 加setTimeOut是为了解决ticket_id传递到子组件的异步问题
        console.log(this.$refs.deliverydetail)
        this.$refs.deliverydetail.$refs.orderarea.getSubCategory()
        this.$refs.deliverydetail.getTicketOrder()
        this.$refs.deliverydetail.getTicketBasicDetail()
      }, 0)
      this.detailShow = true
    },

    async onCreateNewWechatTicket (ticketDetail) {
      this.wechatTicket = {}
      const ticketId = await this.createWechatTicket(ticketDetail)
      console.log('onCreateNewWechatTicket内部：', ticketId)
      // console.log(ticketDetail)
      setTimeout(() => {
        this.$refs.deliverydetail.$refs.orderarea.submitWechatOrder(ticketDetail)
      }, 0)
      // console.log(ticketDetail)
      this.wechatTicket = ticketDetail
      // 这里留着写爬虫
      setTimeout(() => { // 加setTimeOut是为了解决ticket_id传递到子组件的异步问题
        // console.log(this.$refs.deliverydetail)
        console.log(this.wechatTicket)
        this.$refs.deliverydetail.$refs.orderarea.getSubCategory()
        this.$refs.deliverydetail.getTicketOrder()
        this.$refs.deliverydetail.getTicketBasicDetail()
        // this.$refs.deliverydetail.patchWechatTicketPrice()
        // this.$refs.deliverydetail.patchWechatTicketBasicDetail(content)
      }, 0)
      // setTimeout(() => {
      //   this.$refs.deliverydetail.patchWechatTicketPrice()
      // }, 300)
      this.detailShow = true
    },

    // 获取全部菜品汇总信息，厨房贴单用
    async getAllDishGroupBy () {
      const res = await axios.get('/testdishordergroupby/0/')
      console.log(res.data)
      this.groupbyList = res.data
      this.groupbyShow = true
      // const date = new Date()
      // const printData = {}
      // printData.print_id = date.getTime().toString()
      // printData.print_type = printType
      // printData.print_content = JSON.stringify(postContent)
      // axios({
      //   url: '/testprintlist/',
      //   method: 'POST',
      //   data: printData
      // })
    },

    onHideGroupby () {
      this.groupbyShow = false
    },

    handleArrangementShow () {
      // this.arrangementShow = true
      this.$router.push('/arrangement')
    },

    onHideArrangement () {
      this.arrangementShow = false
    }
  },

  watch: {
    activeTab: function () {
      switch (this.activeTab) {
        case 0:
          console.log('tab===2')
          this.getTicket()
          break
        case 1:
          console.log('tab===1')
          this.getTicket()
          break
        case 2:
          console.log('tab===2')
          this.getTicket()
          // this.ticketList = this.ticketList.filter((item) => item.type === 2)
          break
        case 3:
          console.log('tab===3')
          this.getTicket()
          break
        default:
          this.ticketList = this.ticketList.filter((item) => item.type === 2)
          break
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .outer2 {
    // position: fixed;
    // box-sizing: border-box;
    // width: 100%;
    // height: 100vh;
    // top: 0px;
    // padding: 0 0px;
    // background-color: #fff;
    .nav {
      width: 100%;
      height: 50px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      align-items: center;
      .category {
        height: 50px;
        font-weight: 500;
        display: grid;
        align-items: center;
      }
      .active {
        color: rgba($color: #217969, $alpha: 1);
        font-weight: 800;
        border-bottom: 2px solid rgba($color: #217969, $alpha: 1);
      }
    }

    .searchBar {
      height: 40px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      display: grid;
      grid-template-columns: 1fr 9fr 2fr;
      align-items: center;
    }

    .newTicket {
      position: fixed;
      right: 30px;
      bottom: 90px;
      z-index: 9;
      // border: 1px solid black;
      // i {
      //   border-radius: 50%;
      //   background: white;
      //   color: #217969;
      //   font-size: 60px;
      // }
    }

    .slide-enter-active {
      animation: slideDetail 0.2s;
    }

    .slide-leave-active {
      animation: slideDetail 0.2s reverse;
    }

    @keyframes slideDetail {
      0% {
        transform: translateX(100%);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }

    .testslide-enter-active {
      animation: slideTest 0.2s;
    }

    .testslide-leave-active {
      animation: slideTest 0.2s reverse;
    }

    @keyframes slideTest {
      0% {
        transform: translateX(100%);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
</style>
