<template>
  <!-- 第一行 -->
  <div class="topnav" :class="showMore ? 'long': ''">
    <div class="home_page">
      <i class="iconfont icon-zuojiantou" @click="handleHideDetail"></i>
    </div>
    <div class="table_num"  style="font-size: 12px">
      <i class="iconfont icon-bianhao"></i>
      <span id="input_table_num">{{ticketBasicDetail.table_num}}</span>
    </div>
    <div class="platform" style="font-size: 12px">
      <i class="iconfont icon-pingtai"></i>
      <span>{{ platform }}</span>
    </div>
    <div class="name"  style="font-size: 12px">
      <i class="iconfont icon-kb-persion"></i>
      <span>{{ ticketBasicDetail.client_name }}</span>
    </div>
    <div class="time">
      <i class="iconfont icon-shijian"></i>
      <span>{{ ticketBasicDetail.delivery_time }}</span>
    </div>

    <!-- 第二行 -->
    <div class="adress">
      <i class="iconfont icon-tubiaozhizuomoban"></i>
      <p><a :href="'https://www.google.fr/maps/place/'+computedAddress" target="_blank"
      style="text-decoration: none; color:inherit;">
      {{ ticketBasicDetail.client_address }}</a></p>
    </div>
    <div class="home_page">
      <i class="iconfont icon-kuozhan" @click="handleShowMore"></i>
    </div>

    <!-- 第三行 -->
    <div class="telephone">
      <i class="iconfont icon-dianhua"></i>
      <span>{{ ticketBasicDetail.client_telephone }}</span>
    </div>
    <div class="urgent">
      <i class="iconfont icon-jiaji"></i>
      <span>{{ urgent }}</span>
    </div>
    <div class="livreur">
      <i class="iconfont icon-qishou"></i>
      <span>{{ livreur }}</span>
    </div>

    <!-- 第四行 -->
    <div class="ticketNote">
      <i class="iconfont icon-beizhu"></i>
      <p>{{ ticketBasicDetail.ticket_note }}</p>
    </div>

    <div>
      <i class="iconfont icon--tainjia" @click="show=!show"></i>
    </div>

    <!-- 弹出框 -->
    <van-popup v-model="show" class='quickAdd' round :style="{ height: '80%', width: '100%'}">
      <div class="inputArea">
        <div class="table_num">
          <i class="iconfont icon-bianhao"></i>
          <input type="text" v-model="ticketBasicDetail.table_num">
        </div>
        <div class="platform">
          <i class="iconfont icon-pingtai"></i>
          <van-dropdown-menu>
            <van-dropdown-item v-model="ticketBasicDetail.delivery_platform" :options="platformOption" />
          </van-dropdown-menu>
        </div>
        <div class="delivery_time">
          <i class="iconfont icon-shijian"></i>
          <input type="text" v-model="ticketBasicDetail.delivery_time">
        </div>
        <div class="urgent">
          <i class="iconfont icon-jiaji"></i>
          <!-- <input type="text" v-model="ticketBasicDetail.ticket_emergency"> -->
          <van-dropdown-menu>
            <van-dropdown-item v-model="ticketBasicDetail.ticket_emergency" :options="urgenceOption" />
          </van-dropdown-menu>
        </div>
        <div class="name">
          <i class="iconfont icon-kb-persion"></i>
          <input type="text" v-model="ticketBasicDetail.client_name">
        </div>
        <div class="telephone">
          <i class="iconfont icon-dianhua"></i>
          <input type="number" v-model="ticketBasicDetail.client_telephone">
        </div>
        <div class="livreur">
          <i class="iconfont icon-qishou"></i>
          <!-- <input type="text" v-model="ticketBasicDetail.delivery_man"> -->
          <van-dropdown-menu>
            <van-dropdown-item v-model="ticketBasicDetail.delivery_man" :options="livreurOption" />
          </van-dropdown-menu>
        </div>
        <div class="address">
          <i class="iconfont icon-tubiaozhizuomoban"></i>
          <textarea name="" id="" cols="" rows="3" v-model="ticketBasicDetail.client_address"></textarea>
        </div>
        <div class="ticket_note">
          <i class="iconfont icon-beizhu"></i>
          <textarea name="" id="" cols="" rows="3" v-model="ticketBasicDetail.ticket_note"></textarea>
        </div>
      </div>
      <div class='handleArea'>
        <v-btn dark rounded color="#217969" width="100px" class="buttonConfirm" @click="handleConfirm">确认</v-btn>      </div>
      <div class='pasteArea'>
        <textarea v-model="content" type="text" placeholder="时间 | 姓名 | 地址 | 电话 |">
        </textarea>
      </div>
      <div class='handleAreaQuick'>
        <v-btn dark rounded color="#217969" width="100px" class="buttonConfirm" @click="handleQuickConfirm">确认</v-btn>
        <v-btn dark rounded color="#ff4a4a" width="100px" class="buttonCancel" @click="handleCancel">取消</v-btn>
      </div>
    </van-popup>
  </div>
</template>

<script>
import Vue from 'vue'
import { VBtn } from 'vuetify/lib/components'
import 'vuetify/dist/vuetify.min.css'
import { Popup, DropdownMenu, DropdownItem } from 'vant'

Vue.use(Popup).use(DropdownMenu).use(DropdownItem)

export default {
  props: ['ticketBasicDetail'],
  components: {
    VBtn
  },
  data () {
    return {
      showMore: false,
      show: false,
      content: '',
      client: {},
      platformOption: [
        { text: 'Ricepo', value: 1 },
        { text: 'HungryP', value: 2 },
        { text: 'Uber', value: 3 },
        { text: 'JustEat', value: 4 },
        { text: '小程序', value: 5 },
        { text: '微信', value: 6 },
        { text: '官网', value: 7 },
        { text: '微米', value: 8 },
        { text: '方圆', value: 9 },
        { text: '呱呱', value: 10 },
        { text: 'Deliveroo', value: 11 },
        { text: '无', value: 12 }
      ],
      urgenceOption: [
        { text: '普通', value: 0 },
        { text: '优先', value: 1 },
        { text: '紧急', value: 2 }
      ],
      livreurOption: [
        { text: '无', value: 1 },
        { text: '张俊', value: 2 },
        { text: '阿然', value: 3 },
        { text: '老马', value: 4 }
      ]
      // client: {
      //   table_num: this.ticketBasicDetail.table_num,
      //   delivery_time: this.ticketBasicDetail.delivery_time,
      //   name: this.ticketBasicDetail.client_name,
      //   address: this.ticketBasicDetail.client_address,
      //   telephone: this.ticketBasicDetail.client_telephone,
      //   urgent: this.ticketBasicDetail.ticket_emergency,
      //   livreur: this.ticketBasicDetail.delivery_man,
      //   platform: this.ticketBasicDetail.delivery_platform,
      //   ticket_note: this.ticketBasicDetail.ticket_note
      // }
    }
  },
  computed: {
    computedAddress () {
      if (this.ticketBasicDetail.client_address) {
        console.log(typeof (this.ticketBasicDetail.client_address))
        const arr = this.ticketBasicDetail.client_address.split(' ')
        console.log(arr)
        return arr.join('+')
      }
      return ''
    },

    platform () {
      switch (this.ticketBasicDetail.delivery_platform) {
        case 1:
          return 'Ricepo'
        case 2:
          return 'HungryPanda'
        case 3:
          return 'Uber'
        case 4:
          return 'JustEat'
        case 5:
          return '小程序'
        case 6:
          return '微信'
        case 7:
          return '官网'
        case 8:
          return '微米'
        case 9:
          return '方圆食里'
        case 10:
          return '呱呱到家'
        case 11:
          return 'Deliveroo'
        case 12:
          return ''
        default:
          return ''
      }
    },
    urgent () {
      switch (this.ticketBasicDetail.ticket_emergency) {
        case 0:
          return '普通'
        case 1:
          return '优先'
        case 2:
          return '紧急'
        default:
          return '普通'
      }
    },
    livreur () {
      switch (this.ticketBasicDetail.delivery_man) {
        case 1:
          return '无'
        case 2:
          return '张俊'
        case 3:
          return '阿然'
        case 4:
          return '老马'
        case 0:
          return '无'
        default:
          return ''
      }
    }
  },
  methods: {
    handleHideDetail () {
      // console.log(this.$parent.$parent)
      this.$parent.$parent.onHideDetail()
    },
    handleShowMore () {
      this.showMore = !this.showMore
    },
    handleConfirm () {
      this.content = ''
      this.show = false
      this.$emit('patchTicketBasicDetail', this.ticketBasicDetail)
    },
    handleQuickConfirm () {
      console.log(this.content)
      // this.$emit('patchTicketBasicDetailQuickly', this.content)
      // 使用发现子组件只能传一个监听事件给父组件，上面已经传了一个patchTicketBasicDetail
      // 所以这里传一个patchTicketBasicDetailQuickly就接受不到
      // 有待考证
      this.$parent.onPatchTicketBasicDetailQuickly(this.content)
      // console.log(this.ticketBasicDetail)
      this.content = ''
      this.show = false
    },
    handleCancel () {
      this.content = ''
      this.show = false
    }
  }
}
</script>

