<template>
  <div>
    <ul>
      <li v-for="data in this.$store.state.dishes" :key="data.did" class='dish-detail'>
        <div id="firstline">
          <p style="font-weight: bold">{{ data.dname }}</p>
          <p v-show="selectNum(data.dname)">已选 {{ selectNum(data.dname) }}</p>
        </div>
        <div id="secondline">
          <p style="color: #ff4a4a">{{ data.dprice }}</p>
          <div class="quantity">
            <!-- <input type="button" class="minus is_hidden" :disabled="data.selecting===0?true:false" value="-"> -->
            <button class="minus" :style="{'display': data.selecting > 0?'block':'none'}" @click="data.selecting -= 1">-</button>
            <input type="number" class="dish_quantity" :value="data.selecting" :style="{'display': data.selecting > 0?'block':'none'}"
            style=" text-align: center; width: 2em; color: #ff4a4a">
            <button class="add" @click="data.selecting += 1" value='+'>+</button>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from 'axios'
import Vue from 'vue'

// 获取tableId的方法2
function getTableId (that) {
  axios({
    url: `/testemptyticket/${that.$route.params.table_num}/`
  }).then(res => {
    return res.data[0].ticket_id
  }).catch(err => {
    console.log('方法2获取tableId失败', err)
  })
}

export default {
  data () {
    return {
      // dishes: [],
      selectedDish: {}
    }
  },
  computed: {
    // selectNum2 () {
    //   return function (dname) {
    //     let quantity
    //     this.selectedDish.forEach(function (value, key) {
    //       // quantity = Object.keys(value).includes(dname) ? Object.values(value)[0] : '0'
    //       if (Object.keys(value).includes(dname)) {
    //         quantity = Object.values(value)[0]
    //       }
    //     })
    //     return quantity
    //   }
    // }
  },
  methods: {
    handleScroll (ev) {
      console.log(this.$el.children[0].children[0].getBoundingClientRect().top)
      // alert('dish页面发生滚动' + this.$el.children[0].children[0].getBoundingClientRect().top)
      alert('dish页面发生滚动' + ev.target.getBoundingClientRect().top)
    },
    selectNum (dname) {
      let quantity
      if (this.$store.state.selectedDish.length !== undefined) {
        this.$store.state.selectedDish.forEach(function (value, key) {
        // quantity = Object.keys(value).includes(dname) ? Object.values(value)[0] : '0'
          if (Object.keys(value).includes(dname)) {
            quantity = Object.values(value)[0].quantity
          }
        })
        return quantity
      } else {
        return ''
      }
    },
    hancleClick (data) {
      data.selecting += 1
      // console.log(data)
    }
  },
  mounted () {
    // console.log(this.$el.clientWidth)
    // const tt = this.$el.children[0]
    // tt.addEventListener('scroll', this.handleScroll, true)

    // console.log(this.$store.state)
    // 加载页面时根据 vuex 里的 categoryId 获取 菜品 数据
    axios({
      url: `/testdish/${this.$store.state.categoryId}/`
    }).then(res => {
      // console.log(res.data)
      // this.dishes = res.data
      this.$store.commit('changeDishes', res.data)
      // this.$store.state.dishes = res.data.map(function (item) {
      //   // console.log(item)
      //   // item.push({
      //   //   key: 'selecting',
      //   //   value: 0
      //   // }) // 遇到问题：push不进去
      //   // item.selecting = 0 // 遇到问题：通过这种直接等号赋值的方法，添加的新属性没有get和set方法。
      //   Vue.set(item, 'selecting', 0)
      //   return item
      // })
      this.$store.commit('changeSelectingDish', this.$store.state.dishes)
      // console.log(this.$store.state.dishes)
    })
    axios({
      url: `/testticketordergroupby/${this.$route.params.table_num}/`
    }).then(res => {
      console.log('已选菜品groupBy：：：：', res.data)
      // res.data 是 根据单号 从后台查询来的 整单信息
      // res.data[0].tableid[0] 是 tableId
      // console.log(res.data[0].tableid[0])
      this.$store.commit('changeTicketDetail', res.data)
      // console.log(this.$store.state.table.ticketDetail)
      // console.log(this.$store.state.table.ticketLength)
      // 判断 如果 查询来的 整单信息 的长度大于0，则说明 不用新开单
      if (res.data.length > 0) {
        this.$store.commit('changeTableId', res.data[0].tableid[0])
      } else if (getTableId(this)) {
        this.$store.commit('changeTableId', getTableId(this))
      }
      // this.selectedDish = res.data.map(item => {item.dname: item.quantity})
      this.$store.commit('changeSelectedDish', res.data)
      // console.log('测：：：：：：：：：：：', res.data)
      // this.$store.state.selectedDish = res.data.map(function (item) {
      //   var Obj = {}
      //   Obj[item.dname] = { quantity: item.total_quantity[0], price: item.price }
      //   // Vue.set(item, 'price', item.dprice)
      //   // Obj[price] = item.dprice
      //   console.log(Obj)
      //   return Obj
      // })
    })
  },
  watch: {
    // 点击 类别 时根据 vuex 里的 categoryId 获取 菜品 数据
    '$store.state.categoryId': function () {
      axios({
        url: `/testdish/${this.$store.state.categoryId}/`
      }).then(res => {
        // console.log(res.data)
        this.$store.state.dishes = res.data.map(function (item) {
          Vue.set(item, 'selecting', 0)
          return item
        })
        this.$store.commit('changeSelectingDish', this.$store.state.dishes)
        // console.log(this.$store.state.table.ticketDetail)
      })
    },
    '$store.state.table.orderLength': function () {
      axios({
        url: `/testticketordergroupby/${this.$route.params.table_num}/`
      }).then(res => {
        this.$store.commit('changeTicketDetail', res.data)
        if (res.data.length > 0) { this.$store.commit('changeTableId', res.data[0].tableid[0]) }
        // this.selectedDish = res.data.map(item => {item.dname: item.quantity})
        this.$store.state.selectedDish = res.data.map(function (item) {
          var Obj = {}
          // Obj[item.dname] = item.total_quantity[0]
          Obj[item.dname] = { quantity: item.total_quantity[0], price: item.dprice, tax: item.dtax }
          return Obj
        })
        this.$store.state.dishes = this.$store.state.dishes.map(function (item) {
          Vue.set(item, 'selecting', 0)
          return item
        })
        this.$store.commit('changeSelectingDish', this.$store.state.dishes)
      })
      // this.$store.commit('changeSubmit', false)
    },
    '$store.state.table.selecteDish': function () {
      axios({
        url: `/testticketordergroupby/${this.$route.params.table_num}/`
      }).then(res => {
        console.log(res.data)
        // res.data 是 根据单号 从后台查询来的 整单信息
        // res.data[0].tableid[0] 是 tableId
        // console.log(res.data[0].tableid[0])
        this.$store.commit('changeTicketDetail', res.data)
        // console.log(this.$store.state.table.ticketDetail)
        // console.log(this.$store.state.table.ticketLength)
        // 判断 如果 查询来的 整单信息 的长度大于0，则说明 不用新开单
        if (res.data.length > 0) {
          this.$store.commit('changeTableId', res.data[0].tableid[0])
        } else if (getTableId(this)) {
          this.$store.commit('changeTableId', getTableId(this))
        }
        // this.selectedDish = res.data.map(item => {item.dname: item.quantity})
        this.$store.state.selectedDish = res.data.map(function (item) {
          var Obj = {}
          // Obj[item.dname] = item.total_quantity[0]
          Obj[item.dname] = { quantity: item.total_quantity[0], price: item.dprice, tax: item.dtax }
          return Obj
        })
      })
    }
  }
}
</script>
<style lang="scss" scoped>
  .dish-detail {
    background-color: white;
    padding: 10px;
    margin-left: 5px;
    border-bottom: 1px solid rgb(219, 219, 219);
    min-height: 60px;
    display: flex;
    flex-direction: column;;
    justify-content: space-between;
    align-items: flex-start;

    #firstline {
      width: 90%;
      // border: 1px solid black;
      display: flex;
      justify-content: flex-start;
      align-items: center;

      p:nth-child(1) {
        // color: red;
        flex: 5;
        text-align: start;
      }
      p:nth-child(2) {
        flex: 2;
        font-size: 0.5em;
      }
    }

    #secondline {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;

      p {
        flex: 2;
        text-align: start;
      }

      div {
        display: flex;
        justify-content: flex-end;
        place-items: 20px;
        flex: 3;
      }

      .quantity {
        display: grid;
        grid-template-columns: repeat(4,1fr);
        align-items: center;
        .add {
          box-sizing: content-box;
          width: 2em;
          height: 2em;
          border: 10px solid transparent;
          border-radius: 2em;
          background-clip: padding-box;
          // border: 1px solid #3ada3a;
          background-color: rgba($color: #217969, $alpha: 0.8);
          color: #fff;
          // line-height: 2em;
          text-align: center;
          grid-column: 4/5;
        }
        .dish_quantity {
          margin-right: 1em;
          text-align: center;
          margin: 0 auto;
          grid-column: 3/4
        }
        .minus{
          box-sizing: content-box;
          // margin-right: 1em;
          width: 1.9em;
          height: 1.9em;
          // border: 10px solid transparent;
          border-radius: 2em;
          background-clip: padding-box;
          border: 1px solid rgba($color: #217969, $alpha: 0.8);
          background-color: #fff;
          color: #217969;
          text-align: center;
          grid-column: 2/3
        }
      }
    }
  }
</style>
