<template>
  <div class="outer2">

      <div class="header">
        <i class="iconfont icon-shangjiantou1"
        style="text-align: right; margin-left: 0px; border: 2px solid white; border-radius: 50%; font-size:2em; color: black; font-weight: bolder"
        @click="handlePayClick">
        </i>
        <p>桌号：{{ $route.params.table_num }}</p>
        <div @click="payingDishShow = !payingDishShow" class="payingAmount">辅助：{{ payingAmount }}</div>
      </div>

      <transition name='paying'>
        <div class="payingDish" v-show="payingDishShow">
          <div @click="payingDishShow = !payingDishShow" style="text-align: right; margin-right:10px; font-size: 30px">x</div>
          <div style="display: grid; grid-template-columns: 1fr 2fr; align-items: center; border-bottom: 1px solid rgba(255, 255, 255, 0.5)">
            <p @click="payingList=[]">重置</p>
            <h3 class='payingTotal'>总：{{ payingAmount }}</h3>
          </div>

          <ul style="margin-top: 5px">
            <li @click="handlePayingDel(data)" class="payingDetail" v-for="data in payingList" :key="data.id">
              <p>{{ data.name }}</p>
              <p>{{ data.price }}</p>
            </li>
          </ul>
        </div>
      </transition>

      <div class="stickyArea">
        <div id='total'>
          <div id="tax10">
            <p class="taxTitle">10%</p>
            <p>{{ tatalTax10 }}</p>
          </div>
          <div id="tax20">
            <p class="taxTitle">20%</p>
            {{ tatalTax20 }}
          </div>
          <div id="totalTax">
            <p class="taxTitle">总TVA</p>
            {{ tatalTax }}</div>
          <h2 id='totalPrice'>总：{{ totalPrice }}</h2>
        </div>
        <div class="detailArea">
            <div class="detail" style="color: #2CA4B0">
              <p class="title">转账</p>
              <p class="amount">{{ $store.state.payCondition.virement }}</p>
            </div>
            <div class="detail" style="color: #ff4a4a">
              <p class="title">现金</p>
              <p class="amount">{{ $store.state.payCondition.cash }}</p>
            </div>
            <div class="detail" style="color: #217969">
              <p class="title">饭票</p>
              <p class="amount">{{ $store.state.payCondition.ticket }}</p>
            </div>
            <div class="detail" style="color: #b48608">
              <p class="title">刷卡</p>
              <p class="amount">{{ $store.state.payCondition.card }}</p>
            </div>
            <div class="detail">
              <p class="title">支票</p>
              <p class="amount">{{ $store.state.payCondition.check }}</p>
            </div>
            <div class="detail">
              <p class="title">微信</p>
              <p class="amount">{{ $store.state.payCondition.wechat }}</p>
            </div>
            <div class="detail">
              <p class="title">阿里</p>
              <p class="amount">{{ $store.state.payCondition.alipay }}</p>
            </div>
        </div>
        <!-- <div class="pay">
          <div class="totalPay">已付：</div>
          <div class="restPay">剩余：</div>
        </div> -->
      </div>

      <div class="handleArea">
        <div class="handlePay">
          <div class="payAmount">
            <h3>金额：</h3>
            <input ref="inputArea" type="number" @focus="focus($event)" style="width: 90px; height: 40px; text-align: left; border: 1px solid rgba(7, 17, 27, 0.1)" v-model="inputAmount">
            <div class="totalPay">已付：{{ alreadyPay }}</div>
            <div class="restPay">剩余：{{ restPay }}</div>
          </div>
          <div class="payButton">
            <v-btn @click="handlePayByVirement">转账</v-btn>
            <v-btn @click="handlePayByCash">现金</v-btn>
            <div class="history" style='grid-row:1/6; grid-column:3/4; background-color: #eee; border-radius:10px'>
              <p>付款记录</p>
            </div>
            <v-btn @click="handlePayByTicket">饭票</v-btn>
            <v-btn @click="handlePayByCard">刷卡</v-btn>
            <v-btn @click="handlePayByCheck">支票</v-btn>
            <v-btn @click="handlePayByWechat">微信</v-btn>
            <v-btn @click="handlePayByAlipay">阿里</v-btn>
          </div>
        </div>
      </div>

      <!-- <div class="ordered_list">
        <ul>
          <li class="order_container-out" v-for="data in $store.state.selectedDish" :key="data.order_id">
                <h3>{{ Object.keys(data)[0] }}</h3>
                <p class="price">{{ Object.values(data)[0].price }}</p>
                <p class="quantity">{{ Object.values(data)[0].quantity }}份</p>
                <p class="subTotal">{{ (Object.values(data)[0].price * Object.values(data)[0].quantity).toFixed(2) }}</p>

          </li>
        </ul>
      </div> -->

      <div class='ordered_list' ref="orderWrapper">
        <ul>
          <li @click="handlePayingAdd(data)" class="order_container" v-for="data in $store.state.selectedDish" :key="data.order_id">
            <h3 class="dish_name">{{ Object.keys(data)[0] }}</h3>
            <p class="price">{{ Object.values(data)[0].price }}</p>
            <p class="quantity">{{ Object.values(data)[0].quantity }}份</p>
            <p class="subTotal">{{ (Object.values(data)[0].price * Object.values(data)[0].quantity).toFixed(2) }}</p>
          </li>
        </ul>
      </div>

      <div style="position: fixed; bottom: 20px; width: 100%">
        <v-btn rounded class="submit" @click="handleSubmit">提交后台</v-btn>
      </div>
  </div>
</template>

<script>
import Vue from 'vue'
import BScroll from 'better-scroll'
import { VBtn } from 'vuetify/lib'
// import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import axios from 'axios'

function refreshPay (_this) {
  async function getPay (that) {
    const response = await axios({
      url: `/testticketdetail/${that.$route.params.table_num}/`
    })
    console.log(response.data)
    that.$store.commit('changePayCondition', response.data)
  }
  getPay(_this)
}

function pay (method, method2, that) {
  if (that.inputAmount !== '') {
    const num = parseFloat(method2) + parseFloat(that.inputAmount)
    const content = '{' + '"' + method + '"' + ': ' + num + '}'
    console.log(content)
    console.log(JSON.parse(content))

    axios({
      url: `/testpatchticket/${that.$store.state.tableId}/`,
      method: 'PATCH',
      data: JSON.parse(content)
    }).then(() => { refreshPay(that) })
    // that.inputAmount = ''
    that.$refs.inputArea.select()
  }
}

export default {
  components: {
    VBtn
  },
  data () {
    return {
      payingList: [],
      payingDishShow: false,
      inputAmount: ''
    }
  },
  mounted () {
    if (this.$store.state.selectedDish.length !== undefined) {
      this.$nextTick(() => {
        this._initScroll()
      })
      // console.log(this.$store.state.selectedDish)
    }
  },
  computed: {
    totalPrice () {
      // console.log('test:::::::::', this.$store.state.selectedDish)
      let total = 0
      if (this.$store.state.selectedDish.length !== undefined) {
        for (const data of this.$store.state.selectedDish) {
          // console.log(data)
          total += Object.values(data)[0].price * Object.values(data)[0].quantity
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax10 () {
      // console.log('测试：：：：：：：：：：：', this.$store.state.selectedDish.length)
      let total = 0
      if (this.$store.state.selectedDish.length !== undefined) {
        for (const data of this.$store.state.selectedDish) {
          if (Object.values(data)[0].tax === '0.10') {
            total += (Object.values(data)[0].price - (Object.values(data)[0].price / (1 + parseFloat(Object.values(data)[0].tax)))) * Object.values(data)[0].quantity
          }
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax20 () {
      let total = 0
      if (this.$store.state.selectedDish.length !== undefined) {
        for (const data of this.$store.state.selectedDish) {
          if (Object.values(data)[0].tax === '0.20') {
            total += Object.values(data)[0].tax * Object.values(data)[0].quantity
          }
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax () {
      return (parseFloat(this.tatalTax10) + parseFloat(this.tatalTax20)).toFixed(2)
    },
    payingAmount () {
      let total = 0
      this.payingList.forEach((item) => {
        total += parseFloat(item.price)
      })
      return total.toFixed(2)
    },
    alreadyPay () {
      let total = 0
      Object.values(this.$store.state.payCondition).forEach(item => {
        total += parseFloat(item)
      })
      return total
    },
    restPay () {
      const total = this.totalPrice - this.alreadyPay
      return total.toFixed(2)
    }
  },
  methods: {
    focus (event) {
      event.currentTarget.select()
    },
    handlePayClick () {
      this.payingList = []
      this.$store.commit('changePayShow')
    },
    _initScroll () {
      this.orderScroll = new BScroll('.ordered_list', {
        scrollY: true,
        click: true
      })

      // 获取点击的dom元素
      // const dish = this.$refs.orderWrapper
      // dish.addEventListener('click', (evt) => {
      //   console.log(evt.target.parentNode)
      // })
    },
    handlePayingAdd (data) {
      // console.log(Object.keys(data)[0])
      // console.log(Object.values(data)[0].price)
      const date = new Date()
      const dish = {}
      Vue.set(dish, 'id', date.getTime())
      Vue.set(dish, 'name', Object.keys(data)[0])
      Vue.set(dish, 'price', Object.values(data)[0].price)
      this.payingList.push(dish)
      console.log(this.payingList)
    },
    handlePayingDel (data) {
      console.log(this.payingList.indexOf(data))
      const index = this.payingList.indexOf(data)
      this.payingList.splice(index, 1)
    },
    // 转账支付
    handlePayByVirement () {
      pay('pay_by_transfer', this.$store.state.payCondition.virement, this)
    },
    // 现金支付
    handlePayByCash () {
      // axios({
      //   url: `/testpatchticket/${this.$store.state.tableId}/`,
      //   method: 'PATCH',
      //   data: { pay_by_cash: parseFloat(this.$store.state.payCondition.cash) + parseFloat(this.inputAmount) }
      // }).then(() => { refreshPay(this) })
      // this.inputAmount = ''
      pay('pay_by_cash', this.$store.state.payCondition.cash, this)
    },
    handlePayByTicket () {
      pay('pay_by_ticket', this.$store.state.payCondition.ticket, this)
    },
    handlePayByCard () {
      pay('pay_by_card', this.$store.state.payCondition.card, this)
    },
    handlePayByCheck () {
      pay('pay_by_check', this.$store.state.payCondition.check, this)
    },
    handlePayByWechat () {
      pay('pay_by_wechat', this.$store.state.payCondition.wechat, this)
    },
    handlePayByAlipay () {
      pay('pay_by_alipay', this.$store.state.payCondition.alipay, this)
    },
    handleSubmit () {
      // console.log(parseFloat(this.restPay))
      // console.log(parseFloat(this.totalPrice))
      // console.log(parseFloat(this.restPay) === 0 && parseFloat(this.totalPrice) !== 0)
      if (parseFloat(this.restPay) === 0 && parseFloat(this.totalPrice) !== 0) {
        console.log('：：：：：：：：开始进入提交程序')
        var date = new Date()
        var ticketData = {}
        ticketData.finish_time = date.getFullYear().toString() +
                    (((date.getMonth() + 1).toString().length < 2) ? '0' + (date.getMonth() + 1).toString() : (date.getMonth() + 1).toString()) +
                    date.getDate().toString() +
                    (((date.getHours()).toString().length < 2) ? '0' + date.getHours().toString() : date.getHours().toString()) +
                    (((date.getMinutes()).toString().length < 2) ? '0' + date.getMinutes().toString() : date.getMinutes().toString()) +
                    (((date.getSeconds()).toString().length < 2) ? '0' + date.getSeconds().toString() : date.getSeconds().toString())
        ticketData.payment_status = 2
        ticketData.ticket_status = 2
        ticketData.ticket_price = this.totalPrice
        ticketData.ticket_tax10 = this.tatalTax10
        ticketData.ticket_tax20 = this.tatalTax20
        ticketData.ticket_tax = this.tatalTax
        console.log(ticketData)
        axios({
          url: `/testpatchticket/${this.$store.state.tableId}/`,
          method: 'PATCH',
          data: ticketData
        }).then(() => { this.$router.push('/salle') }).catch((err) => console.log(err))
        // that.inputAmount = ''
      } else {
        console.log('没有开始提交')
      }
    }
  },
  beforeDestroy () {
    this.payingList = []
  },
  watch: {
    '$store.state.payShow' () {
      this.$nextTick(() => {
        if (this.$store.state.payShow) {
          if (!this.orderScroll) {
            this._initScroll()
          } else {
            this.orderScroll.refresh()
            console.log('刷新了Bs')
          }
          // console.log(111111111111111)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>

    .outer2{
      position: fixed;
      box-sizing: border-box;
      width: 100%;
      height: 100vh;
      top: 0px;
      z-index: 10;
      padding: 0 0px;
      background-color: #fff;
      overflow-y: auto;
      .header {
        position: fixed;
        top: 0px;
        width: 100%;
        background-color: #fff;
        display: grid;
        grid-template-columns: 1fr 2fr 8fr;
        align-items: center;
        // justify-content: space-evenly;
        .icon-shangjiantou1 {
        transform: rotate(180deg);
        // flex: 0 0 60px;
        }
        .payingAmount {
          text-align: right;
          padding-right: 10px;
        }
      }

      .payingDish {
        position: fixed;
        top: 10px;
        right: 10px;
        width: 200px;
        height: 400px;
        color: white;
        background-color: rgba(7, 17, 27, 0.8);
        z-index: 11;
        border-radius: 10px;
        overflow: hidden;
        overflow-y: auto;
        .payingDetail {
          display: grid;
          align-items: center;
          grid-template-columns: 5fr 3fr;
          grid-auto-rows: 1.5em;
          font-size: 18px;
        }
      }

      .paying-enter-active {
        animation: toggle 0.2s;
      }

      .paying-leave-active {
        animation: toggle 0.2s reverse;
      }

      @keyframes toggle {
        0% {
          width: 0px;
          height: 0px;
        }
        100% {
          width: 200px;
          height: 400px;
        }
      }
      // .order_container-out:nth-last-child(2) {
      //   border-bottom: 1px solid #eee;
      // }

      .stickyArea {
        position: relative;
        top: 35px;
        height: 100px;
        background-color: #fff;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        width: 100%;
        box-sizing: border-box;
        // border: 1px solid black;
        margin: 0;
        #total {
          padding: 5px 10px;
          width: 100%;
          height: 42px;
          display: flex;
          align-items: center;
          // border-bottom: 1px solid #eee;
          // flex-direction: row-reverse;
          .taxTitle {
            font-size: 10px;
          }
          #tax10 {
            flex: 1;
            font-size: 14px;
          }
          #tax20 {
            flex: 1;
            font-size: 14px;
          }
          #totalTax {
            flex: 1;
            font-size: 14px;
          }
          #totalPrice {
            flex: 0 0 130px;
            text-align: end;
          }
        }
        .detailArea {
          // position: sticky;
          height: 50px;
          display: grid;
          grid-template-columns:repeat(7, 1fr);
          grid-column-gap: 0.5em;
          grid-row-gap: 0.2em;
          // border: 1px solid black;
          .detail {
            height: 50px;
            // border: 1px solid black;
          }
        }
        .pay {
          display: flex;
          justify-content: flex-end;
          font-weight: 600;
          .restPay {
            flex: 0 0 130px;
            text-align: end;
          }
        }
      }

      .handleArea {
        position: relative;
        top: 35px;
        margin-top: 5px;
        padding: 5px;
        display: grid;
        grid-template-columns: 1fr;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        // justify-content: space-between;
        .handlePay {
          // border: 1px solid black;
          display: flex;
          flex-direction: column;
          .payAmount {
            display: grid;
            grid-template-columns: 2fr 2fr 3fr 3fr;
            align-items: center;
            // flex-direction: column;
          }
          .payButton {
            margin-top: 10px;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-gap: 1em;
            z-index: 1;
          }
        }
        .history {
          flex: 1;
        }
      }

      .ordered_list {
        position: relative;
        top: 35px;
        height: 230px;
        z-index: 10;
        overflow: hidden;
        // overflow-y: auto;
        // border: 2px solid black;
        // background-color: red;
        box-sizing: border-box;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        padding: 10px;
        .order_container{
            display: grid;
            grid-template-columns: 2fr 1fr 1fr 1fr;
            width: 100%;
            align-items: center;
            grid-auto-rows: 2em;
              .dish_name{
                text-align: start;
              }
              .price {
                text-align: start;
              }
              .quantity{
                font-size: 1em;
                text-align: start;
              }
              .subTotal {
                text-align: end;
              }
        }
      }
    .submit {
      // position: fixed;
      // bottom: 20px;
      margin-top: 10px;
      color: white;
      font-weight: 800;
      background-color: rgba($color: #217969, $alpha: 1);
    }
    }

</style>
