<template>
  <div class="stickyArea">
      <div id='total'>
        <div id="tax10">
          <p class="taxTitle">10%</p>
          <p>{{ tatalTax10 }}</p>
        </div>
        <div id="tax20">
          <p class="taxTitle">20%</p>
          {{ tatalTax20 }}
        </div>
        <div id="totalTax">
          <p class="taxTitle">总TVA</p>
          {{ tatalTax }}</div>
        <h2 id='totalPrice'>总：{{ totalPrice }}</h2>
      </div>
      <div class="detailArea">
          <div class="detail" style="color: #2CA4B0">
            <p class="title">转账</p>
            <p class="amount">{{ totalVirement }}</p>
          </div>
          <div class="detail" style="color: #ff4a4a">
            <p class="title">现金</p>
            <p class="amount">{{ totalCash }}</p>
          </div>
          <div class="detail" style="color: #217969">
            <p class="title">饭票</p>
            <p class="amount">{{ totalTicket }}</p>
            <p style="font-size:10px; margin-top:-5px">({{ totalTicketCount }}张)</p>
          </div>
          <div class="detail" style="color: #b48608">
            <p class="title">刷卡</p>
            <p class="amount">{{ totalCard }}</p>
          </div>
          <div class="detail">
            <p class="title">支票</p>
            <p class="amount">{{ totalCheck }}</p>
          </div>
          <div class="detail">
            <p class="title">微信</p>
            <p class="amount">{{ totalWechat }}</p>
          </div>
          <div class="detail">
            <p class="title">阿里</p>
            <p class="amount">{{ totalAlipay }}</p>
          </div>
      </div>
    </div>
</template>

<script>
export default {
  props: ['ticketList'],
  computed: {
    totalPrice () {
      // console.log('test:::::::::', this.$store.state.selectedDish)
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          // console.log(data)
          total += parseFloat(data.ticket_price)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax10 () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.ticket_tax10)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax20 () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.ticket_tax20)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    tatalTax () {
      return (parseFloat(this.tatalTax10) + parseFloat(this.tatalTax20)).toFixed(2)
    },
    totalVirement () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_transfer)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalCash () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_cash)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalTicket () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_ticket)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalTicketCount () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += data.ticket_restaurant_count
        }
        return total
      } else {
        return 0
      }
    },
    totalCard () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_card)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalCheck () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_check)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalWechat () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_wechat)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    },
    totalAlipay () {
      let total = 0
      if (this.ticketList.length !== undefined) {
        for (const data of this.ticketList) {
          total += parseFloat(data.pay_by_alipay)
        }
        return total.toFixed(2)
      } else {
        return 0
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .stickyArea {
        height: 100px;
        background-color: #fff;
        border-bottom: 1px solid rgba(7, 17, 27, 0.1);
        width: 100%;
        box-sizing: border-box;
        // border: 1px solid black;
        margin: 0;
        #total {
          padding: 5px 10px;
          width: 100%;
          height: 42px;
          display: flex;
          align-items: center;
          // border-bottom: 1px solid #eee;
          // flex-direction: row-reverse;
          .taxTitle {
            font-size: 10px;
          }
          #tax10 {
            flex: 1;
            font-size: 14px;
          }
          #tax20 {
            flex: 1;
            font-size: 14px;
          }
          #totalTax {
            flex: 1;
            font-size: 14px;
          }
          #totalPrice {
            flex: 0 0 130px;
            text-align: end;
          }
        }
        .detailArea {
          height: 50px;
          display: grid;
          grid-template-columns:repeat(7, 1fr);
          grid-column-gap: 0.5em;
          grid-row-gap: 0.2em;
          // border: 1px solid black;
          .detail {
            height: 50px;
            // border: 1px solid black;
          }
        }
      }
</style>
