<template>
  <div class="ticketList">
      <ul>
        <li v-for="data in ticketList" :key="data.ticket_id" @click="handleDetailShow(data)">
          <div class='type'><h3>{{ type(data) }}</h3></div>
          <div class="detail">
            <h3 class="name">{{ title(data) }}</h3>
            <p class="amount">总价：{{ data.ticket_price }}</p>
            <div class="pay" v-if="data.type !== 2">
              <div class="payDetail" style="color: #2CA4B0" v-show="data.pay_by_transfer != '0.00'">
                <p class="title">转账</p>
                <p class="amount">{{ data.pay_by_transfer }}</p>
              </div>
              <div class="payDetail" style="color: #ff4a4a" v-show="data.pay_by_cash != '0.00'">
                <p class="title">现金</p>
                <p class="amount">{{ data.pay_by_cash }}</p>
              </div>
              <div class="payDetail" style="color: #217969" v-show="data.pay_by_ticket != '0.00'">
                <p class="title">饭票</p>
                <p class="amount">{{ data.pay_by_ticket }}</p>
              </div>
              <div class="payDetail" style="color: #b48608" v-show="data.pay_by_card != '0.00'">
                <p class="title">刷卡</p>
                <p class="amount">{{ data.pay_by_card }}</p>
              </div>
              <div class="payDetail" v-show="data.pay_by_check != '0.00'">
                <p class="title">支票</p>
                <p class="amount">{{ data.pay_by_check }}</p>
              </div>
              <div class="payDetail" v-show="data.pay_by_wechat != '0.00'">
                <p class="title">微信</p>
                <p class="amount">{{ data.pay_by_wechat }}</p>
              </div>
              <div class="payDetail" v-show="data.pay_by_alipay != '0.00'">
                <p class="title">阿里</p>
                <p class="amount">{{ data.pay_by_alipay }}</p>
              </div>
            </div>
            <div class="address" v-else-if="data.type === 2">
              {{ data.client_address }}
            </div>
          </div>
          <div class="arrow"><i class="iconfont icon-arrow-right"></i></div>
        </li>
      </ul>
    </div>
</template>

<script>
export default {
  props: ['ticketList'],

  data () {
    return {
      platformOption: [
        { text: 'Ricepo', value: 1 },
        { text: 'HungryP', value: 2 },
        { text: 'Uber', value: 3 },
        { text: 'JustEat', value: 4 },
        { text: '小程序', value: 5 },
        { text: '微信', value: 6 },
        { text: '官网', value: 7 },
        { text: '微米', value: 8 },
        { text: '方圆', value: 9 },
        { text: '呱呱', value: 10 },
        { text: 'Deliveroo', value: 11 },
        { text: '无', value: 12 }
      ]
    }
  },

  computed: {
    type () {
      return function (data) {
        if (data.type === 0) {
          return '堂食'
        } else if (data.type === 1) {
          return '打包'
        } else {
          return '外送'
        }
      }
    },
    platform () {
      return function (data) {
        switch (data.delivery_platform) {
          case 1:
            return ' - Ricepo'
          case 2:
            return ' - HungryPanda'
          case 3:
            return ' - Uber'
          case 4:
            return ' - JustEat'
          case 5:
            return ' - 小程序'
          case 6:
            return ' - 微信'
          case 7:
            return ' - 官网'
          case 8:
            return ' - 微米'
          case 9:
            return ' - 方圆食里'
          case 10:
            return ' - 呱呱到家'
          case 11:
            return ' - Deliveroo'
          case 12:
            return ''
          default:
            return ''
        }
      }
    },
    deliveryMan () {
      return function (data) {
        switch (data.delivery_man) {
          case 1:
            return ''
          case 2:
            return ' - 张俊'
          case 3:
            return ' - 阿然'
          case 4:
            return ' - 老马'
          default:
            return ''
        }
      }
    },
    // 计算订单标题
    title () {
      return function (data) {
        if (data.type === 0) {
          return '桌号：' + data.table_num
        } else if (data.type === 1) {
          return '打包'
        } else {
          return '编号：' + data.table_num + this.platform(data) + this.deliveryMan(data)
        }
      }
    }
  },

  methods: {
    handleDetailShow (data) {
      this.$emit('handleDetailShow', data)
    }
  }
}
</script>

<style lang="scss" scoped>
  .ticketList {
      height: calc(100vh - 365px); // calc(100vh - 250px)在chrome里正好，验证的至ios上多减去115px刚刚好
      box-sizing: border-box;
      overflow: hidden;
      // border: 1px solid black
      ul {
        li {
          min-height: 86px;
          padding: 5px;
          display: grid;
          grid-template-columns: 3fr 12fr 1fr;
          align-items: center;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
          .type {
            // border: 1px solid black;
          }
          .detail {
            // border: 1px solid black;
            display: grid;
            text-align: left;
            grid-row-gap: 3px;
            .name {

            }
            .amount {

            }
            .pay {
              display: grid;
              grid-template-columns: repeat(7, 1fr);
              font-size: 10px;
            }
          }
          .arrow {
            // border: 1px solid black;
          }
        }
      }
    }
</style>
