<template>
  <div :class="$store.state.ticketShow || $store.state.payShow?'noscroll':''">
    <!-- <div style="position: fix; top: 0px; background: white; z-index: 100; height: 40px">order页面</div> -->
    <info></info>
    <div  id='orderArea'>
      <category id='category'/>
      <dish id='dish'/>
    </div>
    <navbar v-show="$store.state.orderNavShow"></navbar>
    <transition name='ticket'>
      <ticket v-show="$store.state.ticketShow"></ticket>
    </transition>
    <transition name='ticket'>
      <pay v-show="$store.state.payShow"></pay>
    </transition>
    <button @click="handleClick" style=" display: none">测试</button>
  </div>
</template>

<script>
import axios from 'axios'
import Info from './Order/Info'
import Category from './Order/Category.vue'
import Dish from './Order/Dish.vue'
import Navbar from './Order/Navbar'
import Ticket from './Order/Ticket'
import Pay from './Order/Pay'

function handleScroll () {
  console.length('order页面发生滚动')
}

export default {
  components: { Category, Dish, Info, Navbar, Ticket, Pay },
  data () {
    return {
      categories: []
    }
  },
  methods: {
    handleClick () {
      // console.log('orderDetail:::::::', this.$store.state.table.orderDetail[0].quantity)
      console.log('changingOrderDetail:::::::', this.$store.state.table.changingOrderDetail[0].checked)
    }
  },
  mounted () {
    window.addEventListener('scroll', handleScroll)
    this.$store.commit('hide', false)
    axios({
      url: '/testticket/' + this.$route.params.table_num + '/'
    }).then(res => {
      console.log('人数', res.data[0].ticket_personquantity)
      // console.log('路径桌号：：：：：', this.$route.params.table_num)
      this.$store.commit('changeTableNum', { table_num: this.$route.params.table_num, orderlength: res.data.length, person_quantity: parseInt(res.data[0].ticket_personquantity) })
      // console.log(this.$store.state.table.orderLength)
    }).catch((err) => {
      console.log('由于ticket内没有order，方法1获取失败', err)
      console.log('测试下是否执行了这句话')
      axios({
        url: `/testticketdetail/${this.$route.params.table_num}/`
      }).then(res => {
        console.log(res.data[0])
        this.$store.commit('changeTableNum', { table_num: this.$route.params.table_num, orderlength: res.data.length, person_quantity: parseInt(res.data[0].person_quantity) })
      }).catch(err => {
        console.log('由于ticket内没有order，方法2获取失败', err)
        this.$store.commit('changeTableNum', { table_num: this.$route.params.table_num, orderlength: 0, person_quantity: 0 })
      })
    })
  },
  beforeDestroy () {
    // 让tabbar回复显示
    this.$store.commit('show')
    this.$store.commit('hideTicketShow')
    this.$store.commit('hidePayShow')
    // this.show()
  },
  watch: {
    '$store.state.ticketShow': function () {
      if (this.$store.state.ticketShow === true) {
        console.log('禁止滑动')
      }
    }
    // '$store.state.tableId': function () {
    //   console.log('检测到人数变化')
    //   console.log('axios请求前：：：：：：：：：：', this.$store.state.table.ticketPersonQuantity)
    //   axios({
    //     url: '/testticket/' + this.$route.params.table_num + '/'
    //   }).then(res => {
    //     console.log(':::::::::::::::', res)
    //     this.$store.commit('changeTableNum', { table_num: this.$route.params.table_num, orderlength: res.data.length, person_quantity: parseInt(res.data[0].ticket_personquantity) })
    //     console.log('axios请求后：：：：：：：：：：', this.$store.state.table.ticketPersonQuantity)
    //   }).catch((err) => {
    //     console.log('axios请求失败：：：：：：：：：：', err)
    //   })
    // }
  }
}
</script>
<style lang="scss" scoped>
  * {
    background-color: white;
  }
  #orderArea {
    top: 65px;
    margin-top: 5px;
    width: 100%;
    // height: calc(100vh - 240px);
    height: 100vh;
    display: flex;
    // border: 1px solid black;
    #category {
      // border: 1px solid black;
      flex: 2;
      // width: 200px;
      overflow-y: auto;
      // display: block;
    }
    #dish {
      // border: 1px solid black;
      flex: 5;
      // width: 200px;
      overflow-y: auto;
      // display: block;
    }
  }
  #ticket_list {
    top: 0px;
    // left: 10px;
    margin-top: 0px;
    z-index: 10;
    height: calc(100vh - 100px);
    width: 100%;
  }
  .ticket-enter-active {
    animation: ticketAnimation 0.1s forwards;
  }
  .ticket-leave-active {
    animation: ticketAnimation 0.1s reverse forwards;
  }
  @keyframes ticketAnimation {
    0% {
      opacity: 0;
      transform: translateY(100vh);
    }
    100% {
      opacity: 100;
      transform: translateY(0px);
    }
  }

  .noscroll {
    overflow: hidden;
    position: fixed;
  }
</style>
