<template>
  <div class="outer">
    <div class="top">
      <i class="iconfont icon-zuojiantou" @click="$parent.handleDetailShow()"></i>
    </div>
    <div class="info">
      <p>单号：{{ticket.ticketNumber}}</p>
      <p>类型：{{ticket.type}}</p>

      <p>昵称：{{ticket.clientName}}</p>
      <div style="display: grid; grid-template-columns: repeat(2, auto); align-items: center">
        <p>时间：</p>
        <p style="font-size:14px;">{{ticket.deliveryTime}}</p>
      </div>

      <!-- <p style="grid-column: 1/3; text-align:start">地址：{{ticket.address}}</p> -->
      <p style="grid-column: 1/3; text-align:start">地址：
      <a :href="'https://www.google.fr/maps/place/'+computedAddress" target="_blank"
      style="text-decoration: none; color:inherit;">
      {{ ticket.address }}</a></p>

      <p>邮编：{{ticket.zipCode}}</p>
      <p>电话：{{ticket.tel}}</p>

      <p style="grid-column: 1/3; text-align:start">下单时间：{{ticket.orderTime}}</p>

      <div style="grid-column: 1/3; text-align:start; display: grid; grid-template-columns: repeat(2, auto">
        <p>备注：</p>
        <p style="color: rgb(255, 74, 74)">{{ticket.ticketNote}}</p>
      </div>
    </div>
    <div class="order">
      <div style="margin-top: 5px; text-align: right;" class="total">
        <p style="font-weight: 600;">Total: {{total}}</p>
        <p v-if="originTotal >= 50">原价：{{originTotal}}</p>
        <p v-if="originTotal >= 100">满100减8</p>
        <p v-else-if="originTotal >= 75">满75减5</p>
        <p v-else-if="originTotal >= 50">满50减3</p>
      </div>
      <ul>
        <li v-for="order in ticket.orders" :key="order.dishName">
          <p style="text-align: start">{{order.dishName}}</p>
          <p :class="{'multi': order.dishCount > 1}">{{order.dishCount}}</p>
          <p>{{order.dishPrice}}</p>
        </li>
      </ul>
    </div>
    <div>
      <v-btn dark rounded color="#217969"
      style="margin-top: 20px"
      @click="handleCrawlInner">
        调取
      </v-btn>
    </div>
  </div>
</template>

<script>
export default {
  props: ['ticket'],
  data () {
    return {
      // originTotal: 0
    }
  },

  computed: {
    computedAddress () {
      if (this.ticket.address) {
        // console.log(typeof (this.ticket.address))
        const arr = this.ticket.address.split(' ')
        // console.log(arr)
        return arr.join('+')
      }
      return ''
    },
    originTotal () {
      let totalP = 0
      if (this.ticket.orders) {
        this.ticket.orders.forEach(order => {
          totalP += Number(order.dishCount) * Number(order.dishPrice)
        })
        return totalP.toFixed(2)
      }
      return 0
    },
    total () {
      let totalP = 0
      if (this.ticket.orders) {
        this.ticket.orders.forEach(order => {
          totalP += Number(order.dishCount) * Number(order.dishPrice)
        })
        if (totalP >= 100) {
          totalP -= 8
        } else if (totalP >= 75) {
          totalP -= 5
        } else if (totalP >= 50) {
          totalP -= 3
        }
        return totalP.toFixed(2)
      }
      return 0
    }
  },

  methods: {
    handleCrawlInner () {
      this.$parent.handleDetailShow()
      this.$parent.$parent.onHideWechat()
      // console.log('测试：：：：：：：：：', this.ticket)
      this.$parent.$parent.onCreateNewWechatTicket(this.ticket)
    }
  }
}
</script>

<style lang="scss" scoped>
  .outer {
    position: fixed;
    top: 0;
    width: 100%;
    height: calc(100vh - 165px);
    z-index: 11;
    background-color: #fff;
    .top {
      margin-top: 10px;
      display: grid;
      justify-content: start;
      align-items: center;
      grid-template-columns: 30px 1fr;
    }
    .info {
      margin: 10px 10px 0 10px;
      padding-bottom: 10px;
      display: grid;
      justify-content: start;
      justify-items: start;
      grid-template-columns: 2fr 3fr;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
    }
    .order {
      margin: 10px 10px 0 10px;
      li {
        display: grid;
        grid-template-columns: 4fr 1fr 1fr;
        align-items: center;
        min-height: 30px;
        .multi {
          color: rgb(255, 74, 74);
        }
      }
      li:nth-child(odd){
        background: #eee;
      }
    }
  }
</style>
