<template>
  <div>
    <ul>
      <li v-for="data in categories" :key="data.category_id"
      :class="[data.category_id == $store.state.categoryId ? 'categorySelect' : 'categoryNoselect']"
      @click="changeCategory(data.category_id)">
        <!-- <p >{{ data.category }}</p> -->
        <p>{{ data.category }}</p>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      categories: []
    }
  },
  mounted () {
    axios({
      url: '/testcategory/'
    }).then(res => {
      // console.log(res.data)
      this.categories = res.data
    })
  },
  methods: {
    changeCategory (categoryid) {
      // console.log(categoryid)
      this.$store.commit('changeCategoryId', categoryid)
      // console.log(this.$store.state.categoryId)
    }
  }
}
</script>
<style lang="scss" scoped>
  li {
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid rgb(219, 219, 219);
  }
  .categorySelect {
    color: rgb(0, 185, 0);
    border-left: 2px solid rgb(0, 185, 0);
    background-color: white
  }
  .categoryNoselect {
    background-color: rgb(245, 245, 245);
  }
</style>
