<template>
  <div class="quantity">
    <button class="minus" :style="{'display': food.selecting?'block':'none'}" @click="handleMinus">-</button>
    <!-- <input type="number" class="dish_quantity" :value="0" v-show="food.selecting>0"
    style=" text-align: center; width: 2em; color: #ff4a4a"> -->
    <span class="dish_quantity" v-show="food.selecting"
    style=" text-align: center; width: 2em; color: #ff4a4a">{{food.selecting}}</span>
    <button class="add" @click="handleAdd" value='+'>+</button>
  </div>
</template>

<script>
import Vue from 'vue'

export default {
  props: ['food'],
  data () {
    return {
      // selecting: 0
    }
  },
  mounted () {

  },
  methods: {
    handleAdd () {
      // console.log(this.food.selecting)
      if (!this.food.selecting) {
        // this.food.selecting = 1
        Vue.set(this.food, 'selecting', 1)
      } else {
        this.food.selecting += 1
      }
    },
    handleMinus () {
      if (this.food.selecting > 0) {
        this.food.selecting -= 1
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .quantity {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    align-items: center;
    .add {
      box-sizing: content-box;
      width: 2em;
      height: 2em;
      border: 10px solid transparent;
      border-radius: 2em;
      background-clip: padding-box;
      // border: 1px solid #3ada3a;
      background-color: rgba($color: #217969, $alpha: 0.8);
      color: #fff;
      // line-height: 2em;
      text-align: center;
      grid-column: 4/5;
    }
    .dish_quantity {
      margin-right: 1em;
      text-align: center;
      margin: 0 auto;
      grid-column: 3/4
    }
    .minus{
      box-sizing: content-box;
      // margin-right: 1em;
      width: 1.9em;
      height: 1.9em;
      // border: 10px solid transparent;
      border-radius: 2em;
      background-clip: padding-box;
      border: 1px solid rgba($color: #217969, $alpha: 0.8);
      background-color: #fff;
      color: #217969;
      text-align: center;
      grid-column: 2/3
    }
  }
</style>
