<template>
  <div class="outer">

    <div class="title">
      <i class="iconfont icon-zuojiantou"
      style="font-size:2em; color: black; font-weight: bolder"
      @click="handleHide">
      </i>
      <p>汇总信息</p>
    </div>

    <div class="dishList">
      <ul>
        <li v-for="item in groupbyList" :key="item.did">
          <p class="emergency">{{ computedEmergency(item.orders) }}</p>
          <p class="quantity" :class="{'plusOne': item.total_quantity[0] > 1}">{{ item.total_quantity[0] }}</p>
          <p class="dish_name">{{ item.dname }}</p>
          <p v-show="noteShow(item.orders)" style="white-space: pre-wrap; text-align: left">{{ note(item.orders) }}</p>
        </li>
      </ul>
    </div>

    <div style="position: fixed; bottom: 20px; width: 100%">
      <v-btn rounded class="submit" @click="btnClickPrint(2)">打印</v-btn>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'
export default {
  props: ['groupbyList'],

  computed: {
    computedEmergency () {
      return function (orders) {
        console.log(orders.every(order => order.ticket_emergency[0] === 0))
        if (orders.every(order => order.ticket_emergency[0] === 0)) {
          return ''
        } else {
          return '优先'
        }
      }
    },

    noteShow () {
      return function (orders) {
        return !orders.every(ele => ele.o_note === '无备注') // 如果orders里面的每个order的备注内容，则得到true
      }
    },

    note () {
      return function (orders) {
        let note = ''
        orders.forEach(element => {
          if (element.o_note !== '无备注') { note += element.quantity + '份' + element.o_note + ' ;\n' }
        })
        return note
      }
    }
  },

  methods: {
    handleHide () {
      this.$emit('hideGroupby')
    },

    // 打印按钮
    btnClickPrint (printType) {
      console.log(this.groupbyList)
      for (const dish of this.groupbyList) {
        if (!dish.orders.every(ele => ele.o_note === '无备注')) {
          let note = ''
          dish.orders.forEach(element => {
            if (element.o_note !== '无备注') { note += '  ' + element.quantity + '份' + element.o_note + ' ;\n' }
            if (element.ticket_emergency[0] !== 0) { emergency = 1 }
          })
          Vue.set(dish, 'note_content', note)
        }
        let emergency = 0
        dish.orders.forEach(element => {
          if (element.ticket_emergency[0] !== 0) { emergency = 1 }
        })
        Vue.set(dish, 'emergency', emergency)
      }
      const postContent = this.groupbyList
      console.log(postContent)
      const date = new Date()
      const printData = {}
      printData.print_id = date.getTime().toString()
      printData.print_type = printType
      printData.print_content = JSON.stringify(postContent)
      axios({
        url: '/testprintlist/',
        method: 'POST',
        data: printData
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .outer {
    box-sizing: border-box;
    margin-bottom: 0px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    position: fixed;
    top: 0px;
    z-index:10;
    height: calc(100vh - 0px);
    width: 100%;
    background-color: #fff;
    transition: 0.2s;
    display: flex;
    flex-direction: column;
    .title {
      display: grid;
      // grid-template-columns: repeat(2, 10 auto);
      grid-template-columns: 30px auto;
      grid-column-gap: 10px;
      align-items: center;
      p {
        margin-left: -40px;
      }
    }
    .dishList {
      position: relative;
      // top: 35px;
      height: 550px;
      z-index: 10;
      overflow: hidden;
      overflow-y: auto;
      // border: 1px solid black;
      // background-color: red;
      box-sizing: border-box;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      padding: 10px;
      li {
        display: grid;
        grid-template-columns: 2fr 1fr 7fr 5fr;
        width: 100%;
        align-items: center;
        min-height: 30px;
        .quantity{
          font-size: 1em;
          text-align: end;
        }
        .plusOne {
          color: red;
          font-weight: 600;
        }
        .dish_name {
          margin-left: 30px;
          text-align: start;
        }
      }
      li:nth-child(odd) {
        background-color: rgba(7, 17, 27, 0.1);;
      }
    }
  }
</style>
