<template>
  <div class="outer2" id="ticket_list">
    <div style="display: flex; align-items: center">
      <i class="iconfont icon-shangjiantou1"
     style="text-align: right; margin-left: 0px; border: 2px solid white; border-radius: 50%; font-size:2em; color: black; font-weight: bolder"
     @click="handleTicketClick">
     </i>
    <p>桌号：{{ $route.params.table_num }}</p>
    </div>

    <div id="ordered_list">
      <div v-for="data in $store.state.table.changingOrderDetail" :key="data.order_id">
        <div class="order_container">
          <input type="checkbox" class="check" v-model="data.checked">
          <div class="order">
            <div class="dish_name">{{ data.dname }}</div>
            <input type="button" class="minus2" :disabled="data.quantity===0?true:false" value="-" @click="data.quantity--">
            <input type="number" class="dish_quantity" v-model.number="data.quantity">
            <input type="button" class="add2" value="+" @click="data.quantity++">
            <div class="dish_note">{{ data.note }}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="note" :class="isShow?'indentation':'extention'">
      <p v-for="data in baseNoteList" :key="data.id" :class="['note' + data.note_category_id,
      selectingNum.includes(data.id)?'checked':'']"
      @click="changeChecked(data.id, data.note_content)">
        {{ data.note_content }}
      </p>
    </div>

    <i class="iconfont icon-shangjiantou1"
     style="margin-left: 15px; border-radius: 50%; font-size:1em; color: black; font-weight: bolder"
     :style="{ transform: isShow?'rotate(180deg)':'' }"
     :class="isShow ? 'down' : 'up'"
     @click="isShow = !isShow">
     </i>

    <div id="order_submit">
        <p @click="handleSubmit">提交</p>
        <p @click="showPopup">删整单</p>
    </div>

    <!-- <van-cell is-link @click="showPopup">展示弹出层</van-cell> -->
    <van-popup v-model="show" id='delete_submit' round position="bottom" :style="{ height: '30%', width: '100%'}">
      <p @click="deleteAll">删所有菜</p>
      <p @click="deleteTable">删除桌子</p>
    </van-popup>
  </div>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'
import { Popup } from 'vant'

Vue.use(Popup)

function notePatch (that, index) {
  if (that.$store.state.table.changingOrderDetail[index].checked === true) {
    // console.log('this.selectingNoteContent::::::::::', this.selectingNoteContent.join(', '))
    if (that.selectingNoteContent.length !== 0) {
      console.log('note patch请求')
      // refreshPageTicket(that)
      // refreshPageOrder(that)
      axios({
        url: `/testpatch/${that.$store.state.table.changingOrderDetail[index].order_id}/`,
        method: 'PATCH',
        data: { o_note: that.selectingNoteContent.join(', ') }
      }).then(res => {
        console.log('note patch请求成功')
        // console.log(res.data)
        // refreshPageTicket(that)
        // refreshPageOrder(that)
        // setTimeout((refreshPageTicket(that), refreshPageOrder(that)), 0)
      })
    } else {
      console.log('由于this.selectingNoteContent为空，不执行note patch请求')
      // refreshPageTicket(that)
      // refreshPageOrder(that)
    }
  } else {
    // refreshPageTicket(that)
    // refreshPageOrder(that)
  }
}

function refreshPageTicket (that) {
  console.log('刷新ticket页面')
  axios({
    url: '/testticket/' + that.$route.params.table_num + '/'
  }).then(res => {
    console.log(res.data)
    that.$store.commit('changeOrderDetail', res.data)
    that.$store.commit('changeChangingOrderDetail', res.data)
    console.log('order详情：：：：：：：：：：：', that.$store.state.table.orderDetail)
  })
}

function refreshPageOrder (that) {
  console.log('刷新order页面')
  axios({
    url: `/testticketordergroupby/${that.$route.params.table_num}/`
  }).then(res => {
    console.log(res.data)
    // res.data 是 根据单号 从后台查询来的 整单信息
    // res.data[0].tableid[0] 是 tableId
    // console.log(res.data[0].tableid[0])
    that.$store.commit('changeTicketDetail', res.data)
    that.$store.commit('changeSelectedDish', res.data)
    // that.$store.commit('changeSelectingDish', that.$store.state.dishes)
    // that.$store.state.selectedDish = res.data.map(function (item) {
    //   var Obj = {}
    //   Obj[item.dname] = item.total_quantity[0]
    //   return Obj
    // })
  })
}

export default {
  data () {
    return {
      baseNoteList: [],
      selectedNote: [],
      selectingNum: [],
      selectingNoteContent: [],
      isShow: true,
      show: false
    }
  },
  mounted () {
    // 获取 baseNote
    axios({
      url: '/testbasenote/'
    }).then(res => {
      this.baseNoteList = res.data
      this.selectedNote = res.data.map((item) => {
        Vue.set(item, 'selecting', false)
        return item
      })
    })
  },
  methods: {
    handleTicketClick () {
      this.$store.commit('changeTicketShow')
      axios({
        url: '/testticket/' + this.$route.params.table_num + '/'
      }).then(res => {
        this.$store.commit('changeOrderDetail', res.data)
        this.$store.commit('changeChangingOrderDetail', res.data)
        // console.log('order详情：：：：：：：：：：：', this.$store.state.table.orderDetail)
        // console.log(this.$store.state.table.orderLength)
      })
    },
    noteCategory (data) {
      return { 'data.note_category_id': true }
    },
    changeChecked (id, content) {
      console.log(content)
      var current = this.selectingNum.indexOf(id)
      // console.log(this.selectingNum)
      if (current !== -1) {
        this.selectingNum.splice(current, 1)
      } else {
        this.selectingNum.push(id)
      }
      var current2 = this.selectingNoteContent.indexOf(content)
      console.log('current2::::::::', current2)
      if (current2 !== -1) {
        this.selectingNoteContent.splice(current2, 1)
      } else {
        this.selectingNoteContent.push(content)
      }
      // console.log(this.selectingNum)
      console.log(this.selectingNoteContent)
    },
    handleSubmit () {
      this.$store.state.table.changingOrderDetail.forEach((item, index) => {
        // console.log('changingOrderDetail::::', this.$store.state.table.changingOrderDetail[index].dname, this.$store.state.table.changingOrderDetail[index].quantity)
        // console.log('orderDetail::::::::::::', this.$store.state.table.orderDetail[index].dname, this.$store.state.table.orderDetail[index].quantity)
        // console.log('检测checked:::::::', this.$store.state.table.changingOrderDetail[index].checked)
        // notePatch(this, index)
        if (this.$store.state.table.changingOrderDetail[index].quantity === this.$store.state.table.orderDetail[index].quantity &&
         this.$store.state.table.changingOrderDetail[index].note === this.$store.state.table.orderDetail[index].note) {
          console.log('相等,无需patch')
          // refreshPageTicket(this)
          // refreshPageOrder(this)
          notePatch(this, index)
        } else {
          console.log('不相等，需判断是patch或delete')
          if (this.$store.state.table.changingOrderDetail[index].quantity === 0) {
            console.log('this.$store.state.table.changingOrderDetail[index].quantity:::::', this.$store.state.table.changingOrderDetail[index].quantity)
            console.log('delete请求')
            axios({
              url: `/testpatch/${this.$store.state.table.changingOrderDetail[index].order_id}/`,
              method: 'DELETE',
              data: { quantity: this.$store.state.table.changingOrderDetail[index].quantity }
            }).then(res => {
              // console.log('执行到。then')
              console.log(res.data)
              notePatch(this, index, refreshPageTicket, refreshPageOrder)
              // refreshPageTicket(this)
              // refreshPageOrder(this)
            })
          } else {
            console.log('patch请求')
            axios({
              url: `/testpatch/${this.$store.state.table.changingOrderDetail[index].order_id}/`,
              method: 'PATCH',
              data: { quantity: this.$store.state.table.changingOrderDetail[index].quantity }
            }).then(res => {
              // console.log('执行到。then')
              // console.log(res.data)
              // this.$nextTick(() => {
              //   notePatch(this, index, refreshPageTicket, refreshPageOrder)
              // })
              notePatch(this, index, refreshPageTicket, refreshPageOrder)
              // refreshPageTicket(this)
              // refreshPageOrder(this)
            })
          }
        }
      })
      this.selectingNoteContent = []
      this.selectingNum = []
      console.log(this.selectingNoteContent)
      setTimeout(() => {
        refreshPageTicket(this)
        refreshPageOrder(this)
      }, 100)
    },
    showPopup () {
      this.show = true
    },
    deleteAll () {
      if (this.$store.state.table.orderDetail.length > 0) {
        this.$store.state.table.orderDetail.forEach(item => {
          // console.log(item.order_id)
          axios({
            url: `/testpatch/${item.order_id}/`,
            method: 'DELETE'
          }).then(res => {
            console.log('删除后的回调函数:::', res.data)
            axios({
              url: '/testticket/' + this.$route.params.table_num + '/'
            }).then(res => {
              console.log(res.data)
              this.$store.commit('changeOrderDetail', res.data)
              this.$store.commit('changeChangingOrderDetail', res.data)
              refreshPageOrder(this)
            })
          })
        })
      }
    },
    deleteTable () {
      axios({
        url: `/testpatchtable/${this.$store.state.tableId}/`,
        method: 'DELETE'
      }).then(res => {
        this.$router.push('/salle')
      }).catch(err => {
        console.log(err.message)
      })
    }
  },
  computed: {
    noteCategory2 () {
      return function (data) {
        console.log(data.note_category_id)
        return data.note_category_id
      }
    }
  }
}
</script>
<style lang="scss" scoped>
  .outer2{
    // position: absolute;
    box-sizing: border-box;
    margin-bottom: 0px;
    padding-left: 10px;
    padding-top: 10px;
    position: fixed;
    top: 0px;
    z-index:10;
    height: calc(100vh - 0px);
    width: 100%;
    background-color: #fff;
    transition: 0.3s;
    display: flex;
    flex-direction: column;
    // overflow-y:auto;
    // border: 1px solid black;
  }

.to_front{
  background-color:red;
  position: fixed;
  top: 0px;
  z-index: 10;
}

#ordered_list{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 1em;
    flex-shrink: 0;
    // max-height: calc(100vh - 350px);
    overflow-y: auto;
    max-height: calc(75vh - 280px);
    // border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}

#ordered_list .order{
    width: 100%;
    padding: 5px 0px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #eee;
}

#ordered_list .order_container{
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
}

#ordered_list .order_container input{
    border: 1px solid #ddd;
    width: 25px;
    height: 25px;
}

#ordered_list .order .dish_name{
    flex-basis: 40%;
}

#ordered_list .order .add2{
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: 1px solid rgba($color: #217969, $alpha: 0.8);
    background-color: rgba($color: #217969, $alpha: 0.8);
    color: #fff;
}

#ordered_list .order .minus2{
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: 1px solid rgba($color: #217969, $alpha: 0.8);
    background-color: #fff;
    color: rgba($color: #217969, $alpha: 0.8);
}

#ordered_list .order .dish_quantity{
    border: 1px solid #eee;
    text-align: center;
    width: 2em;
    height: 1.8em;
    font-size: 0.8em;
}

#ordered_list .order .dish_note{
    flex-basis: 30%;
    font-size: 15px;
}

  .outer2 .note{
    flex-shrink: 0;
    margin-top: 20px;
    width: 95%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    // height: 30px;
    // overflow: hidden;
    // transition: 0.3s;
  }

  .outer2 .note p{
    flex-basis: max-content;
    border: 1px solid #ddd;
    margin-left: 5px;
    margin-top: 5px;
  }

  .outer2 .note2{
    color: red;
  }

  .outer2 .note3{
    color: green;
  }

  .outer2 .note4{
    color: blue;
  }

  .outer2 .note5{
    color: darkorange;
  }

  .outer2 .note6{
      color: purple;
  }

  #order_submit{
      flex-shrink: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      margin-top: 1em;
  }

  #order_submit p:first-child{
      border: 1px solid rgba($color: #217969, $alpha: 0.8);
      background-color: rgba($color: #217969, $alpha: 0.8);
      color: #fff;
      width: 50px;
      border-radius: 10px;
      line-height: 40px;
      text-align: center;
      flex-basis: 30%;
  }

  #order_submit p:last-child{
      border: 1px solid red;
      background-color: red;
      color: #fff;
      width: 50px;
      border-radius: 10px;
      line-height: 40px;
      text-align: center;
      flex-basis: 30%;
  }

  .note .checked{
      background-color: #bbb;
  }

  .icon-shangjiantou1 {
    transform: rotate(180deg);
  }

  .indentation {
    // animation-name: animation1;
    // animation-duration: 1.3s;
    // animation-timing-function: ease-in;
    animation: animation1 0.2s forwards;
    // height: 30px;
    overflow: hidden;
  }

  @keyframes animation1 {
    0% {
      height: 150px;
      overflow: hidden;
    }
    100% {
      height: 30px;
      overflow: hidden;
    }
  }

  .extention {
    animation: animation2 0.2s forwards;
    // height: 150px;
    overflow: hidden;
    // transform: all 0.3s;
  }

  @keyframes animation2 {
    0% {
      height: 30px;
      overflow: hidden;
    }
    100% {
      height: 150px;
      overflow: hidden;
    }
  }

  .up {
    animation: toggleup 0.2s;
    transform: rotate(0deg)
  }
  .down {
    animation: toggledown 0.2s;
    transform: rotate(180deg);
  }
  @keyframes toggledown {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(180deg);
    }
  }
  @keyframes toggleup {
    0% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }

  #delete_submit{
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 1em;
    p:first-child {
      border: 1px solid rgba($color: #217969, $alpha: 0.8);
      background-color: rgba($color: #217969, $alpha: 0.8);
      color: #fff;
      width: 50px;
      border-radius: 10px;
      line-height: 40px;
      text-align: center;
      flex-basis: 30%;
    }
    p:last-child{
      border: 1px solid red;
      background-color: red;
      color: #fff;
      width: 50px;
      border-radius: 10px;
      line-height: 40px;
      text-align: center;
      flex-basis: 30%;
    }
  }
</style>
