<template>
  <div class="outer2" id="ticket_list">
    <div class="topNav">
      <i class="iconfont icon-shangjiantou1"
      style="text-align: right; margin-left: 0px; border: 2px solid white; border-radius: 50%; font-size:2em; color: black; font-weight: bolder"
      @click="handleTicketClick">
      </i>
      <p class="tableNum">桌号：{{ ticketBasicDetail.table_num }}</p>
      <div class="allChange">
        <p>整单</p>
        <input type="checkbox" class="check" v-model='checked' @change="handleAllChange">
      </div>
    </div>

    <div id="ordered_list">
      <div v-for="data in ticketOrder" :key="data.order_id">
        <div class="order_container">
          <input type="checkbox" class="check" v-model="data.checked">
          <div class="order">
            <div class="dish_name">{{ data.dname }}</div>
            <input type="button" class="minus2" :disabled="data.quantity===0?true:false" value="-" @click="handleMinus(data)">
            <input type="number" class="dish_quantity" v-model.number="data.quantity">
            <input type="button" class="add2" value="+" @click="handleAdd(data)">
            <div class="dish_note">{{ data.note }}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="note" :class="isShow?'indentation':'extention'">
      <p v-for="data in baseNoteList" :key="data.id" :class="['note' + data.note_category_id,
      selectingNum.includes(data.id)?'checked':'']"
      @click="changeChecked(data.id, data.note_content)">
        {{ data.note_content }}
      </p>
    </div>

    <i class="iconfont icon-shangjiantou1"
     style="margin-left: 15px; border-radius: 50%; font-size:1em; color: black; font-weight: bolder"
     :style="{ transform: isShow?'rotate(180deg)':'' }"
     :class="isShow ? 'down' : 'up'"
     @click="isShow = !isShow">
     </i>

    <div id="order_submit">
        <v-btn dark rounded color="#217969" width="100px" @click="handleSubmit2">提交</v-btn>
        <v-btn dark rounded color="#ff4a4a" width="100px" @click="showPopup">删整单</v-btn>
        <v-btn dark rounded color="#1E88E5" width="100px" @click="btnClickPrint(0)">打印C</v-btn>
        <v-btn dark rounded color="#1E88E5" width="100px" @click="btnClickPrint(1)">打印S</v-btn>
    </div>

    <!-- <div class="print">
        <v-btn dark rounded color="#217969" width="100px" @click="btnClickPrint">打印</v-btn>
    </div> -->

    <!-- <van-cell is-link @click="showPopup">展示弹出层</van-cell> -->
    <van-popup v-model="show" id='delete_submit' round position="bottom" :style="{ height: '30%', width: '100%'}">
      <!-- <p @click="deleteAll">删所有菜</p> -->
      <v-btn dark rounded color="#217969" width="100px" @click="deleteAll">删所有菜</v-btn>
      <!-- <p @click="deleteTable">删除桌子</p> -->
      <v-btn dark rounded color="#ff4a4a" width="100px" @click="deleteTable">删除订单</v-btn>
    </van-popup>
  </div>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'
import { VBtn } from 'vuetify/lib/components'
import 'vuetify/dist/vuetify.min.css'
import { Popup } from 'vant'
// import { getLodop } from '../../utils/LodopFuncs' // 导入打印模块

Vue.use(Popup)

function notePatch (that, index) {
  if (that.ticketOrder[index].checked === true) {
    console.log('this.selectingNoteContent::::::::::', that.selectingNoteContent.join(', '))
    if (that.selectingNoteContent.length !== 0) {
      console.log('note patch请求')
      axios({
        url: `/testpatch/${that.ticketOrder[index].order_id}/`,
        method: 'PATCH',
        data: { o_note: that.selectingNoteContent.join(', ') }
      }).then(res => {
        that.selectingNoteContent = []
        that.selectingNum = []
        console.log(that.selectingNoteContent)
        console.log('note patch请求成功')
        that.$parent.getTicketBasicDetail()
        that.$parent.getTicketOrder()
      })
    } else {
      console.log('由于this.selectingNoteContent为空，不执行note patch请求')
      that.$parent.getTicketBasicDetail()
      that.$parent.getTicketOrder()
      // that.$parent.getSubCategory()
    }
  } else {
    that.selectingNoteContent = []
    that.selectingNum = []
    console.log(that.selectingNoteContent)
    that.$parent.getTicketBasicDetail()
    that.$parent.getTicketOrder()
    // that.$parent.getSubCategory()
  }
}

export default {
  props: ['ticketBasicDetail', 'ticketOrder', 'ticket_id'], // ticketBasicDetail: 桌号等信息，无菜品信息。TicketOrder: 非汇总的orders内容
  components: {
    VBtn
  },
  data () {
    return {
      checked: false,
      baseNoteList: [],
      selectedNote: [],
      selectingNum: [],
      selectingNoteContent: [],
      isShow: true,
      show: false
    }
  },
  mounted () {
    // 获取 baseNote
    axios({
      url: '/testbasenote/'
    }).then(res => {
      this.baseNoteList = res.data
      this.selectedNote = res.data.map((item) => {
        Vue.set(item, 'selecting', false)
        return item
      })
    })
  },
  methods: {
    handleAllChange () { // 全选菜品
      if (this.checked) {
        this.ticketOrder.forEach(item => {
          item.checked = true
        })
      } else {
        this.ticketOrder.forEach(item => {
          item.checked = false
        })
      }
    },

    handleAdd (data) { // +号按钮
      if (!data.oldQuantity) {
        data.oldQuantity = data.quantity
        data.quantity += 1
      } else {
        data.quantity += 1
      }
      // console.log('旧个数：', data.oldQuantity, '新个数：', data.quantity)
    },

    handleMinus (data) { // -号按钮
      if (!data.oldQuantity) {
        data.oldQuantity = data.quantity
        data.quantity -= 1
      } else {
        data.quantity -= 1
      }
      // console.log('旧个数：', data.oldQuantity, '新个数：', data.quantity)
    },

    handleTicketClick () { // 控制Ticket.vue隐藏
      this.$emit('handelTicketShow')
      axios({
        url: '/testticket/' + this.$route.params.table_num + '/'
      }).then(res => {
        this.$store.commit('changeOrderDetail', res.data)
        this.$store.commit('changeChangingOrderDetail', res.data)
        // console.log('order详情：：：：：：：：：：：', this.$store.state.table.orderDetail)
        // console.log(this.$store.state.table.orderLength)
      })
    },

    noteCategory (data) {
      return { 'data.note_category_id': true }
    },

    // 操作 已选中的note 的数组
    changeChecked (id, content) {
      console.log(content)
      var current = this.selectingNum.indexOf(id)
      // console.log(this.selectingNum)
      if (current !== -1) {
        this.selectingNum.splice(current, 1)
      } else {
        this.selectingNum.push(id)
      }
      var current2 = this.selectingNoteContent.indexOf(content)
      console.log('current2::::::::', current2)
      if (current2 !== -1) {
        this.selectingNoteContent.splice(current2, 1)
      } else {
        this.selectingNoteContent.push(content)
      }
      // console.log(this.selectingNum)
      console.log(this.selectingNoteContent)
    },

    // 修改菜品备注
    async notePatch () {
      this.ticketOrder.forEach((item, index) => {
        if (item.checked === true) {
          // console.log(item.dname, '进行notePatch。', 'selectingNoteContent：', this.selectingNoteContent.join(', '))
          return axios({
            url: `/testpatch/${item.order_id}/`,
            method: 'PATCH',
            data: { o_note: this.selectingNoteContent.join(', ') }
          })
        } else {
          // console.log(this.selectingNoteContent)
          // console.log(item.dname, '无需notePatch')
        }
      })
    },

    async patchQuantity (index) {
      const res = await axios({
        url: `/testpatch/${this.ticketOrder[index].order_id}/`,
        method: 'PATCH',
        data: { quantity: this.ticketOrder[index].quantity }
      })
      console.log(res)
      return res
    },

    // 修改菜品数量
    async quantityPatch () {
      await this.ticketOrder.forEach((item, index) => {
        if (item.quantity === item.oldQuantity || item.oldQuantity === undefined) {
          console.log('数量相等,无需patch')
        } else {
          console.log('不相等，需判断是patch或delete')
          if (this.ticketOrder[index].quantity === 0) {
            console.log('this.$store.state.table.changingOrderDetail[index].quantity:::::', this.ticketOrder[index].quantity)
            console.log('delete请求')
            axios({
              url: `/testpatch/${this.ticketOrder[index].order_id}/`,
              method: 'DELETE'
              // data: { quantity: this.ticketOrder[index].quantity } // 删除理应不用data，测试下
            })
          } else {
            console.log('patch请求')
            // notePatch(this, index) // 原先这里先进行notePatch,现在notePatch和quantityPatch分开执行了
            axios({
              url: `/testpatch/${this.ticketOrder[index].order_id}/`,
              method: 'PATCH',
              data: { quantity: this.ticketOrder[index].quantity }
            })
            // this.patchQuantity(index)
          }
        }
      })
    },

    // 提交修改的新方法
    async handleSubmit2 () {
      if (this.selectingNoteContent.length === 0) {
        await this.quantityPatch()
        // console.log(res)
        // setTimeout(() => {
        //   // this.$parent.getTicketBasicDetail()
        //   this.$parent.getTicketOrder()
        //   this.$parent.$refs.orderarea.getSubCategory()
        //   this.checked = false
        // }, 100)
        // this.$nextTick(() => {
        //   setTimeout(() => {
        //     console.log(this.$parent.$refs.bottomnav.total)
        //     this.$parent.patchTicketPrice()
        //   }, 1000)
        //   console.log(this.$parent.$refs.bottomnav.total)
        // })
        await this.$parent.$refs.orderarea.getSubCategory()
        this.checked = false
        await this.$parent.getTicketOrder()
        console.log(this.ticketOrder)
        setTimeout(() => {
          this.$parent.patchTicketPrice()
          console.log(this.ticketBasicDetail)
        }, 500)
        setTimeout(() => {
          this.$parent.getTicketBasicDetail()
        }, 600)
      } else {
        await this.notePatch()
        await this.quantityPatch()
        // setTimeout(() => {
        //   // this.$parent.getTicketBasicDetail()
        //   this.$parent.getTicketOrder()
        //   this.$parent.getSubCategory()
        //   this.selectingNoteContent = []
        //   this.selectingNum = []
        //   this.checked = false
        // }, 100)
        // this.$nextTick(() => {
        //   setTimeout(() => {
        //     console.log(this.$parent.$refs.bottomnav.total)
        //     this.$parent.patchTicketPrice()
        //   }, 1000)
        //   console.log(this.$parent.$refs.bottomnav.total)
        // })
        await this.$parent.$refs.orderarea.getSubCategory()
        this.checked = false
        await this.$parent.patchTicketPrice()
        await this.$parent.getTicketOrder()
        setTimeout(() => {
          this.$parent.patchTicketPrice()
        }, 1000)
        this.selectingNoteContent = []
        this.selectingNum = []
      }
    },

    handleSubmit () { // 旧方法，已废弃
      this.ticketOrder.forEach((item, index) => {
        // console.log(item)
        console.log(item.quantity)
        console.log(item.oldQuantity)
        if (item.quantity === item.oldQuantity || item.oldQuantity === undefined) {
          console.log('相等,无需patch')
          // refreshPageTicket(this)
          // refreshPageOrder(this)
          notePatch(this, index)
        } else {
          console.log('不相等，需判断是patch或delete')
          if (this.ticketOrder[index].quantity === 0) {
            console.log('this.$store.state.table.changingOrderDetail[index].quantity:::::', this.ticketOrder[index].quantity)
            console.log('delete请求')
            axios({
              url: `/testpatch/${this.ticketOrder[index].order_id}/`,
              method: 'DELETE'
              // data: { quantity: this.ticketOrder[index].quantity } // 删除理应不用data，测试下
            }).then(res => {
              // console.log('执行到。then')
              console.log(res.data)
              notePatch(this, index)
              // refreshPageTicket(this)
              // refreshPageOrder(this)
            })
          } else {
            console.log('patch请求')
            notePatch(this, index)
            axios({
              url: `/testpatch/${this.ticketOrder[index].order_id}/`,
              method: 'PATCH',
              data: { quantity: this.ticketOrder[index].quantity }
            }).then(res => {
              // console.log('执行到。then')
              // console.log(res.data)
              // this.$nextTick(() => {
              //   notePatch(this, index, refreshPageTicket, refreshPageOrder)
              // })
              // notePatch(this, index)
              // refreshPageTicket(this)
              // refreshPageOrder(this)
            })
          }
        }
      })
      // this.selectingNoteContent = []
      // this.selectingNum = []
      // console.log(this.selectingNoteContent)

      this.$parent.getSubCategory() // 最后刷新dish的groupBy数据
    },

    showPopup () {
      this.show = true
    },

    // 供异步函数deleteAll使用
    handleDelete () {
      if (this.ticketOrder.length > 0) {
        this.ticketOrder.forEach(item => {
          // console.log(item.order_id)
          axios({
            url: `/testpatch/${item.order_id}/`,
            method: 'DELETE'
          })
        })
      }
    },

    // 删除所有菜品
    async deleteAll () {
      await this.handleDelete()
      this.$parent.getSubCategory()
      setTimeout(() => {
        this.$parent.getTicketOrder()
        this.$parent.patchTicketPrice()
      }, 500)
      // console.log(this.show)
      this.show = false
      // console.log(this.show)
    },

    async deleteTable () {
      await this.handleDelete()
      console.log('即将删除:::::::::::', this.ticketBasicDetail.ticket_id)
      axios({
        url: `/testpatchtable/${this.ticketBasicDetail.ticket_id}/`,
        method: 'DELETE'
      }).then(res => {
        // this.$router.push('/delivery/')
        this.show = false
        // this.$router.go(0)
        this.$emit('hideDetail2')
      }).catch(err => {
        console.log(err.message)
      })
    },

    btnClickPrint (printType) {
      const postContent = this.ticketBasicDetail // 获取ticket基本信息,例如客户姓名,电话等
      console.log(postContent)
      postContent.orders = this.ticketOrder // 获取未经groupBy的菜品数据
      postContent.ordersGroupBy = this.$parent.$refs.orderarea.ticketDetail.data // 获取groupBy后的菜品数据
      console.log(postContent)
      const date = new Date()
      const printData = {}
      printData.print_id = date.getTime().toString()
      printData.print_type = printType
      printData.print_content = JSON.stringify(postContent)
      axios({
        url: '/testprintlist/',
        method: 'POST',
        data: printData
      })
    }
  },
  computed: {
    noteCategory2 () {
      return function (data) {
        console.log(data.note_category_id)
        return data.note_category_id
      }
    },
    ticketDetail () {
      return this.$parent.$children[1].ticketDetail
    }
  }
}
</script>
<style lang="scss" scoped>
  .outer2{
    // position: absolute;
    box-sizing: border-box;
    margin-bottom: 0px;
    padding-left: 10px;
    padding-top: 10px;
    position: fixed;
    top: 0px;
    z-index:10;
    height: calc(100vh - 0px);
    width: 100%;
    background-color: #fff;
    transition: 0.2s;
    display: flex;
    flex-direction: column;
    // overflow-y:auto;
    // border: 1px solid black;
    .topNav {
      display: grid;
      grid-template-columns: repeat(8, 1fr);
      align-items: center;
      .tableNum {
        grid-column: 2/4;
        display: flex;
      }
      .allChange {
        grid-column: 4/6;
        display: flex;
        align-items: center;
      }
    }
  }

.to_front{
  background-color:red;
  position: fixed;
  top: 0px;
  z-index: 10;
}

#ordered_list{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size: 1em;
    flex-shrink: 0;
    // max-height: calc(100vh - 350px);
    overflow-y: auto;
    max-height: calc(75vh - 280px);
    // border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}

#ordered_list .order{
    width: 100%;
    padding: 5px 0px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #eee;
}

#ordered_list .order_container{
    display: flex;
    flex-direction: row;
    width: 100%;
    align-items: center;
}

#ordered_list .order_container input{
    border: 1px solid #ddd;
    width: 25px;
    height: 25px;
}

#ordered_list .order .dish_name{
    flex-basis: 40%;
}

#ordered_list .order .add2{
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: 1px solid rgba($color: #217969, $alpha: 0.8);
    background-color: rgba($color: #217969, $alpha: 0.8);
    color: #fff;
}

#ordered_list .order .minus2{
    width: 30px;
    height: 30px;
    border-radius: 15px;
    border: 1px solid rgba($color: #217969, $alpha: 0.8);
    background-color: #fff;
    color: rgba($color: #217969, $alpha: 0.8);
}

#ordered_list .order .dish_quantity{
    border: 1px solid #eee;
    text-align: center;
    width: 2em;
    height: 1.8em;
    font-size: 0.8em;
}

#ordered_list .order .dish_note{
    flex-basis: 30%;
    font-size: 15px;
}

  .outer2 .note{
    flex-shrink: 0;
    margin-top: 20px;
    width: 95%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    // height: 30px;
    // overflow: hidden;
    // transition: 0.3s;
  }

  .outer2 .note p{
    flex-basis: max-content;
    border: 1px solid #ddd;
    margin-left: 5px;
    margin-top: 5px;
  }

  .outer2 .note2{
    color: red;
  }

  .outer2 .note3{
    color: green;
  }

  .outer2 .note4{
    color: blue;
  }

  .outer2 .note5{
    color: darkorange;
  }

  .outer2 .note6{
      color: purple;
  }

  #order_submit{
      flex-shrink: 0;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-row-gap: 1em;
      // flex-direction: row;
      justify-items: center;
      // justify-content: end;
      margin-top: 1em;
  }

  // #order_submit p:first-child{
  //     border: 1px solid rgba($color: #217969, $alpha: 0.8);
  //     background-color: rgba($color: #217969, $alpha: 0.8);
  //     color: #fff;
  //     width: 50px;
  //     border-radius: 10px;
  //     line-height: 40px;
  //     text-align: center;
  //     flex-basis: 30%;
  // }

  // #order_submit p:last-child{
  //     border: 1px solid red;
  //     background-color: red;
  //     color: #fff;
  //     width: 50px;
  //     border-radius: 10px;
  //     line-height: 40px;
  //     text-align: center;
  //     flex-basis: 30%;
  // }

  .note .checked{
      background-color: #bbb;
  }

  .icon-shangjiantou1 {
    transform: rotate(180deg);
  }

  .indentation {
    // animation-name: animation1;
    // animation-duration: 1.3s;
    // animation-timing-function: ease-in;
    animation: animation1 0.2s forwards;
    // height: 30px;
    overflow: hidden;
  }

  @keyframes animation1 {
    0% {
      height: 150px;
      overflow: hidden;
    }
    100% {
      height: 30px;
      overflow: hidden;
    }
  }

  .extention {
    animation: animation2 0.2s forwards;
    // height: 150px;
    overflow: hidden;
    // transform: all 0.3s;
  }

  @keyframes animation2 {
    0% {
      height: 30px;
      overflow: hidden;
    }
    100% {
      height: 150px;
      overflow: hidden;
    }
  }

  .up {
    animation: toggleup 0.2s;
    transform: rotate(0deg)
  }
  .down {
    animation: toggledown 0.2s;
    transform: rotate(180deg);
  }
  @keyframes toggledown {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(180deg);
    }
  }
  @keyframes toggleup {
    0% {
      transform: rotate(180deg);
    }
    100% {
      transform: rotate(0deg);
    }
  }

  #delete_submit{
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 1em;
    p:first-child {
      border: 1px solid rgba($color: #217969, $alpha: 0.8);
      background-color: rgba($color: #217969, $alpha: 0.8);
      color: #fff;
      width: 50px;
      border-radius: 10px;
      line-height: 40px;
      text-align: center;
      flex-basis: 30%;
    }
    p:last-child{
      border: 1px solid red;
      background-color: red;
      color: #fff;
      width: 50px;
      border-radius: 10px;
      line-height: 40px;
      text-align: center;
      flex-basis: 30%;
    }
  }
</style>
