<template>
  <div class="outer">
    <div class="topNav">
      <i class="iconfont icon-zuojiantou" @click="HideArrangement"></i>
      <p class="tableNum">调度页面</p>
    </div>

    <div class="nav">
      <div class='livreur' :class="[ activeTab === 0 ? 'active' : '']" @click="activeTab = 0"><p>全部</p></div>
      <div class='livreur' :class="[ activeTab === 2 ? 'active' : '']" @click="activeTab = 2"><p>张俊</p></div>
      <div class='livreur' :class="[ activeTab === 4 ? 'active' : '']" @click="activeTab = 4"><p>老马</p></div>
      <div class='livreur' :class="[ activeTab === 3 ? 'active' : '']" @click="activeTab = 3"><p>阿然</p></div>
    </div>

    <div class="handle">
      <v-btn color="#1E88E5" dark>
        <v-icon  @click="openMap">mdi-map-marker-radius-outline</v-icon>
      </v-btn>
      <v-btn color="#1E88E5" dark>
        <v-icon @click="handlePasteShow">mdi-content-paste</v-icon>
      </v-btn>
      <v-btn color="#217969" dark @click="distributeOrder(2)">分配给张</v-btn>
      <v-btn  color="#217969" dark @click="distributeOrder(4)">分配给马</v-btn>
      <v-btn  color="#217969" dark @click="distributeOrder(3)">分配给然</v-btn>

    </div>

    <div class="handle2">
      <!-- <input type="checkbox" class="check" v-model='allChecked'> -->
      <v-checkbox v-model='allChecked' color="#217969"></v-checkbox> 全选
    </div>

    <div class="deliveryList">
      <ul>
        <li v-for="(data, index) in deliveryList" :key="data.ticket_id">
          <div class='type'><v-checkbox v-model="data.checked" color="#217969"></v-checkbox></div>
          <div class="detail"  @click="handleDetailShow(data)">
            <h3 class="name">{{ title(data) }}</h3>
            <p class="amount">总价：{{ data.ticket_price }}</p>
            <div class="pay" v-if="data.type !== 2">
              <div class="payDetail" style="color: #2CA4B0" v-show="data.pay_by_transfer != '0.00'">
                <p class="title">转账</p>
                <p class="amount">{{ data.pay_by_transfer }}</p>
              </div>
              <div class="payDetail" style="color: #ff4a4a" v-show="data.pay_by_cash != '0.00'">
                <p class="title">现金</p>
                <p class="amount">{{ data.pay_by_cash }}</p>
              </div>
              <div class="payDetail" style="color: #217969" v-show="data.pay_by_ticket != '0.00'">
                <p class="title">饭票</p>
                <p class="amount">{{ data.pay_by_ticket }}</p>
              </div>
              <div class="payDetail" style="color: #b48608" v-show="data.pay_by_card != '0.00'">
                <p class="title">刷卡</p>
                <p class="amount">{{ data.pay_by_card }}</p>
              </div>
              <div class="payDetail" v-show="data.pay_by_check != '0.00'">
                <p class="title">支票</p>
                <p class="amount">{{ data.pay_by_check }}</p>
              </div>
              <div class="payDetail" v-show="data.pay_by_wechat != '0.00'">
                <p class="title">微信</p>
                <p class="amount">{{ data.pay_by_wechat }}</p>
              </div>
              <div class="payDetail" v-show="data.pay_by_alipay != '0.00'">
                <p class="title">阿里</p>
                <p class="amount">{{ data.pay_by_alipay }}</p>
              </div>
            </div>
            <div class="address" v-else-if="data.type === 2">
              {{ data.client_address }}
            </div>
          </div>
          <div class="arrow">
            <i class="iconfont icon-shangjiantou1" @click.stop="ticketUp(index)"></i>
            <i class="iconfont icon-xiajiantou" @click.stop="ticketDown(index)"></i>
          </div>
        </li>
      </ul>
    </div>

    <transition name=testslide>
      <delivery-detail ref="deliverydetail" :detailShow='detailShow' :ticket_id='ticketId'
      v-show="detailShow" @hidedetail='onHideDetail'> </delivery-detail>
    </transition>

    <div class="readyForCopy" v-show="pasteShow">
      <textarea v-model="content" type="text" placeholder="复制信息"></textarea>
      <div class="btnArea">
        <v-btn color="#217969" rounded dark @click="copyToClip(content)">复制</v-btn>
        <v-btn dark rounded color="#ff4a4a" @click="pasteShow = false">关闭</v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'
import BScroll from 'better-scroll'
import DeliveryDetail from '../views/DeliveryOrder'

export default {
  props: ['arrangementShow'],

  components: {
    DeliveryDetail
  },

  data () {
    return {
      deliveryList: [],
      activeTab: 0,
      ticketId: '',
      pasteShow: false,
      detailShow: false, // 订单详情,下单页面
      platformOption: [
        { text: 'Ricepo', value: 1 },
        { text: 'HungryP', value: 2 },
        { text: 'Uber', value: 3 },
        { text: 'JustEat', value: 4 },
        { text: '小程序', value: 5 },
        { text: '微信', value: 6 },
        { text: '官网', value: 7 },
        { text: '微米', value: 8 },
        { text: '方圆', value: 9 },
        { text: '呱呱', value: 10 },
        { text: 'Deliveroo', value: 11 },
        { text: '无', value: 12 }
      ]
    }
  },

  mounted () {
    this.getTicket()
  },

  computed: {
    platform () {
      return function (data) {
        switch (data.delivery_platform) {
          case 1:
            return ' - Ricepo'
          case 2:
            return ' - HungryPanda'
          case 3:
            return ' - Uber'
          case 4:
            return ' - JustEat'
          case 5:
            return ' - 小程序'
          case 6:
            return ' - 微信'
          case 7:
            return ' - 官网'
          case 8:
            return ' - 微米'
          case 9:
            return ' - 方圆食里'
          case 10:
            return ' - 呱呱到家'
          case 11:
            return ' - Deliveroo'
          case 12:
            return ''
          default:
            return ''
        }
      }
    },

    // 计算订单标题
    title () {
      return function (data) {
        if (data.type === 0) {
          return '桌号：' + data.table_num
        } else if (data.type === 1) {
          return '打包'
        } else {
          return '编号：' + data.table_num + this.platform(data) + this.deliveryMan(data)
        }
      }
    },

    // 计算送餐员姓名
    deliveryMan () {
      return function (data) {
        switch (data.delivery_man) {
          case 1:
            return ''
          case 2:
            return ' - 张俊'
          case 3:
            return ' - 阿然'
          case 4:
            return ' - 老马'
          default:
            return ''
        }
      }
    },

    // 全选框
    allChecked: {
      get () {
        return this.deliveryList.every(item => item.checked)
      },
      set (value) {
        this.deliveryList.map(item => (item.checked = value))
      }
    },

    // textarea中的内容
    content () {
      let txt = ''
      for (const ticket of this.deliveryList) {
        if (ticket.checked === true) {
          txt += '=================\n' +
          ticket.delivery_time + ' | ' + ticket.client_name +
          ' | ' + ticket.client_address + ' | ' + ticket.client_telephone +
          ' | ' + ticket.ticket_note + ' | ' + ticket.ticket_price + '欧' + ' | ' +
          '\n=================\n\n'
        }
      }
      return txt
    },

    // google地图链接
    mapAddress () {
      let txt = 'https://www.google.fr/maps/dir/86+Rue+de+Richelieu,+75002+Paris'
      for (const ticket of this.deliveryList) {
        if (ticket.checked === true) {
          txt += '/' + ticket.client_address.split(' ').join('+')
        }
      }
      return txt
    }
  },

  methods: {
    HideArrangement () {
      // this.deliveryScroll.destroy()
      // this.$emit('hideArrangement')
      this.$router.push('/delivery')
    },

    handlePasteShow () {
      this.pasteShow = true
    },

    // 获取外送订单
    async getTicket () {
      const res = await axios({
        url: '/testticketlistnopaied/'
      })
      // console.log(res.data) // 测试axios用
      if (this.activeTab === 0) {
        // console.log('等于0')
        this.deliveryList = Object.values(res.data).filter(item => item.type === 2)
      } else {
        // console.log(this.activeTab)
        // Object.values(res.data).forEach(item => console.log(item.delivery_man))
        this.deliveryList = Object.values(res.data).filter(item => item.type === 2 && item.delivery_man === this.activeTab)
      }

      // 为deliveryList添加checked属性（单选框用）
      let i = 0
      for (const item of this.deliveryList) {
        Vue.set(item, 'checked', false)
        Vue.set(item, 'number', i)
        i++
      }
      console.log(this.deliveryList)

      // 为 外送订单列表 添加滚动
      this.$nextTick(() => {
        if (!this.deliveryScroll) {
          this._initScroll()
        } else {
          this.deliveryScroll.refresh()
        }
      })
    },

    // 分配订单给送餐员
    async distributeOrder (deliveryMan) {
      for (const ticket of this.deliveryList) {
        if (ticket.checked === true) {
          await axios({
            url: `/testpatchticket/${ticket.ticket_id}/`,
            method: 'PATCH',
            data: { delivery_man: deliveryMan }
          })
        }
      }
      this.getTicket()
    },

    // 排序函数
    sortDelivery () {
      function compare (property) {
        return function (obj1, obj2) {
          var value1 = obj1[property]
          var value2 = obj2[property]
          return value1 - value2 // 升序
        }
      }
      this.deliveryList = this.deliveryList.sort(compare('number'))
    },

    // 排列单子顺序--向上
    ticketUp (index) {
      if (index !== 0) {
        console.log(this.deliveryList[index])
        this.deliveryList[index].number -= 1
        this.deliveryList[index - 1].number += 1
        this.sortDelivery()
      }
    },

    // 排列单子顺序--向下
    ticketDown (index) {
      // console.log(this.deliveryList[index])
      // console.log('index:', index)
      // console.log('length:', this.deliveryList.length)
      if (index < this.deliveryList.length - 1) {
        this.deliveryList[index].number += 1
        this.deliveryList[index + 1].number -= 1
        this.sortDelivery()
      }
    },

    // 初始化BetterScroll
    _initScroll () {
      this.deliveryScroll = new BScroll('.deliveryList', {
        scrollY: true,
        click: true
      })
    },

    // 打开谷歌地图
    openMap () {
      this.copyToClip(this.mapAddress)
      window.open(this.mapAddress, '_blank')
    },

    onHideDetail (data) {
      this.getTicket()
      this.detailShow = false
    },

    // 点击打开订单详情 detail页面
    handleDetailShow (data) {
      console.log(data.ticket_id)
      this.ticketId = data.ticket_id
      setTimeout(() => { // 加setTimeOut是为了解决ticket_id传递到子组件的异步问题
        this.$refs.deliverydetail.$refs.orderarea.getSubCategory()
        this.$refs.deliverydetail.getTicketOrder()
        this.$refs.deliverydetail.getTicketBasicDetail()
      }, 0)
      this.detailShow = !this.detailShow
      // this.$router.push(`/delivery/${data.ticket_id}`)
    },

    // 一键复制内容至剪贴板
    copyToClip (copyContent) {
      var aux = document.createElement('textarea')
      // aux.setAttribute('value', this.content) // 坑：注意！！！这样写不行，妈的死活不换行
      aux.value = copyContent
      document.body.appendChild(aux)
      aux.select()
      document.execCommand('copy')
      document.body.removeChild(aux)
    }
  },

  watch: {
    activeTab: function () {
      switch (this.activeTab) {
        case 0:
          // console.log('tab===2')
          this.getTicket()
          break
        case 2:
          // console.log('tab===4')
          this.getTicket()
          break
        case 3:
          // console.log('tab===2')
          this.getTicket()
          break
        case 4:
          // console.log('tab===3')
          this.getTicket()
          break
        default:
          break
      }
    },
    arrangementShow: function () {
      if (this.arrangementShow) {
        console.log('测试：：：：')
        this.getTicket(this.activeTab)
      }
    }
  }
}
</script>

<style lang="scss" scoped>
  .outer{
    box-sizing: border-box;
    margin-bottom: 0px;
    padding-left: 10px;
    padding-top: 10px;
    position: fixed;
    top: 0px;
    z-index:10;
    height: calc(100vh - 0px);
    width: 100%;
    background-color: #fff;
    transition: 0.2s;
    display: flex;
    flex-direction: column;
    input[type="checkbox"] {
      width: 30px;
      height: 30px;
    }
    .topNav {
      display: grid;
      grid-template-columns: 30px 1fr;
      align-items: center;
      p {
        margin-left: -30px;
      }
    }

    .nav {
      width: 100%;
      height: 50px;
      border-bottom: 1px solid rgba(7, 17, 27, 0.1);
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      align-items: center;
      .livreur {
        height: 50px;
        font-weight: 500;
        display: grid;
        align-items: center;
      }
      .active {
        color: rgba($color: #217969, $alpha: 1);
        font-weight: 800;
        border-bottom: 2px solid rgba($color: #217969, $alpha: 1);
      }
    }

    .handle {
      margin-top: 10px;
      min-height: 30px;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
      align-items: center;
      justify-items: center;
      button {
        // width: 50%;
        width: 40px;
      }
    }

    .handle2 {
      min-height: 30px;
      display: grid;
      grid-template-columns: 35px 1fr 1fr 1fr 1fr 1fr;
      align-items: center;
      justify-items: center;
      button {
        width: 50%;
      }
    }

    .deliveryList {
      height: calc(100vh - 305px); // calc(100vh - 250px)在chrome里正好，验证的至ios上多减去115px刚刚好
      box-sizing: border-box;
      overflow: hidden;
      // border: 1px solid black
      ul {
        li {
          min-height: 86px;
          padding: 5px;
          display: grid;
          grid-template-columns: 50px 12fr 1fr;
          align-items: center;
          border-bottom: 1px solid rgba(7, 17, 27, 0.1);
          .type {
            // border: 1px solid black;
          }
          .detail {
            // border: 1px solid black;
            display: grid;
            text-align: left;
            grid-row-gap: 3px;
            .name {

            }
            .amount {

            }
            .pay {
              display: grid;
              grid-template-columns: repeat(7, 1fr);
              font-size: 10px;
            }
          }
          .arrow {
            // border: 1px solid black;
            display: grid;
            i {
              font-size: 30px;
            }
          }
        }
      }
    }

    .readyForCopy {
      position: absolute;
      // top: calc(100vh - 0px);
      top: 0px;
      width: 100%;
      height: calc(100vh - 165px );
      background-color: #fff;
      textarea {
        width: 100%;
        height: 100%;
        border: 1px solid rgba(7,17,27,0.1);
        color: black;
        text-align: left;
      }
      .btnArea {
        display: grid;
        grid-template-columns: 1fr 1fr;
        justify-items: center;
      }
    }

    animation: slidePaste 0.1;

    @keyframes slidePaste {
      0% {
        transform: translateY(100%);
        opacity: 0;
      }
      100% {
        transform: translateY(0);
        opacity: 1;
      }
    }

    .testslide-enter-active {
      animation: slideTest 0.2s;
    }

    .testslide-leave-active {
      animation: slideTest 0.2s reverse;
    }

    @keyframes slideTest {
      0% {
        transform: translateX(100%);
        opacity: 0;
      }
      100% {
        transform: translateX(0);
        opacity: 1;
      }
    }
  }
</style>
